// src/game/useTurnEngine.jsx
import React from 'react'

// Pista
import { TRACK_LEN } from '../data/track'

// Modal system
import { useModal } from '../modals/ModalContext'

// Modais de jogo
import ERPSystemsModal from '../modals/ERPSystemsModal'
import TrainingModal from '../modals/TrainingModal'
import DirectBuyModal from '../modals/DirectBuyModal'
import InsideSalesModal from '../modals/InsideSalesModal'
import ClientsModal from '../modals/BuyClientsModal'
import ManagerModal from '../modals/BuyManagerModal'
import FieldSalesModal from '../modals/BuyFieldSalesModal'
import BuyCommonSellersModal from '../modals/BuyCommonSellersModal'
import MixProductsModal from '../modals/MixProductsModal'
import SorteRevesModal from '../modals/SorteRevesModal'
import FaturamentoDoMesModal from '../modals/FaturamentoMesModal'
import DespesasOperacionaisModal from '../modals/DespesasOperacionaisModal'
import InsufficientFundsModal from '../modals/InsufficientFundsModal'
import RecoveryModal from '../modals/RecoveryModal'
import BankruptcyModal from '../modals/BankruptcyModal'

// Regras & helpers puros
import {
  applyDeltas,
  applyTrainingPurchase,
  crossedTile,
  countManagerCerts,
  hasBlue,
  hasPurple,
  hasYellow,
  capacityAndAttendance, // (importado caso queira usar para depurar HUD)
  computeDespesasFor,
  computeFaturamentoFor,
  countAlivePlayers,
  findNextAliveIdx,
} from './gameMath'

/**
 * Hook do motor de turnos.
 * Recebe estados do App e devolve handlers (advanceAndMaybeLap, onAction, nextTurn).
 */
export function useTurnEngine({
  players, setPlayers,
  round, setRound,
  turnIdx, setTurnIdx,
  roundFlags, setRoundFlags,
  isMyTurn,
  isMine,
  myUid, meId,
  myCash,
  current,
  broadcastState,
  appendLog,
  turnLock,
  setTurnLockBroadcast,
  gameOver, setGameOver,
  winner, setWinner,
  setShowBankruptOverlay,
}) {
  // ===== Modais =====
  const { pushModal, awaitTop, closeTop } = useModal?.() || {}

  //  contagem de modais abertas (para saber quando destravar turno)
  const [modalLocks, setModalLocks] = React.useState(0)
  const modalLocksRef = React.useRef(0)
  React.useEffect(() => { modalLocksRef.current = modalLocks }, [modalLocks])
  
  //  CORREO: Flag para indicar que uma modal est sendo aberta (evita race condition)
  const openingModalRef = React.useRef(false)
  
  //  CORREO: Ref para rastrear se h uma mudana de turno em progresso
  const turnChangeInProgressRef = React.useRef(false)
  
  //  CORREO: Ref para timeout de segurana do turnLock
  const turnLockTimeoutRef = React.useRef(null)
  
  //  CORREO: Ref para rastrear quando a ltima modal foi fechada
  // Isso garante que h um delay antes de mudar o turno, dando tempo para todas as modais serem fechadas
  const lastModalClosedTimeRef = React.useRef(null)

  //  CORREO: Normaliza players para garantir ordem consistente
  // Seat  IMUTVEL aps atribudo no start - nunca reatribui seat existente
  const normalizePlayers = React.useCallback((players) => {
    if (!Array.isArray(players) || players.length === 0) return players
    
    // Cria cpia para no mutar o original
    const arr = [...players].filter(Boolean)
    
    // Verifica se TODOS possuem seat vlido
    const hasSeat = arr.every(p => Number.isInteger(p.seat))
    
    // Ordena: se todos tm seat, ordena por seat; seno, ordena por id
    let ordered = hasSeat 
      ? arr.sort((a, b) => a.seat - b.seat)
      : arr.sort((a, b) => String(a?.id ?? a?.player_id ?? '').localeCompare(String(b?.id ?? b?.player_id ?? '')))
    
    // Preenche seats faltantes SEM alterar os existentes
    let nextSeat = 0
    const used = new Set(ordered.filter(p => Number.isInteger(p.seat)).map(p => p.seat))
    
    ordered = ordered.map(p => {
      if (Number.isInteger(p.seat)) {
        return p // Preserva seat existente
      }
      // Atribui prximo seat disponvel
      while (used.has(nextSeat)) nextSeat++
      used.add(nextSeat)
      return { ...p, seat: nextSeat++ }
    })
    
    // Reordena por seat aps preencher faltantes
    ordered = ordered.sort((a, b) => a.seat - b.seat)
    
    return ordered
  }, [])

  //  CORREO: Players ordenados (memoizado) para uso em toda a lgica
  const playersOrdered = React.useMemo(() => normalizePlayers(players), [players, normalizePlayers])

  //  Sincronizao de modalLocks entre jogadores
  React.useEffect(() => {
    if (isMyTurn) {
      // S o jogador da vez pode ter modais abertas
      console.log('[DEBUG] modalLocks sync - isMyTurn:', isMyTurn, 'modalLocks:', modalLocks)
    } else {
      // Outros jogadores devem ter modalLocks = 0
      if (modalLocks > 0) {
        console.log('[DEBUG] modalLocks sync - resetando modalLocks para 0 (no  minha vez)')
        setModalLocks(0)
        modalLocksRef.current = 0
        openingModalRef.current = false
      }
    }
  }, [isMyTurn, modalLocks])
  
  //  CORREO: Timeout de segurana para turnLock (evita travamento infinito)
  React.useEffect(() => {
    if (turnLock) {
      // Limpa timeout anterior se existir
      if (turnLockTimeoutRef.current) {
        clearTimeout(turnLockTimeoutRef.current)
      }
      
      // Define timeout de segurana (30 segundos)
      turnLockTimeoutRef.current = setTimeout(() => {
        const currentLockOwner = lockOwnerRef.current
        const isLockOwner = String(currentLockOwner || '') === String(myUid)
        const currentModalLocks = modalLocksRef.current
        const currentOpening = openingModalRef.current
        
        console.warn('[DEBUG]  TIMEOUT DE SEGURANA - turnLock ativo h mais de 30s', {
          isLockOwner,
          currentModalLocks,
          currentOpening,
          lockOwner: currentLockOwner
        })
        
        // Se sou o dono do lock e no h modais, fora liberao
        if (isLockOwner && currentModalLocks === 0 && !currentOpening) {
          console.warn('[DEBUG]  FORANDO LIBERAO DO TURNLOCK (timeout de segurana)')
          setTurnLockBroadcast(false)
          turnChangeInProgressRef.current = false
        }
      }, 30000) // 30 segundos
    } else {
      // Limpa timeout quando turnLock  liberado
      if (turnLockTimeoutRef.current) {
        clearTimeout(turnLockTimeoutRef.current)
        turnLockTimeoutRef.current = null
      }
    }
    
    return () => {
      if (turnLockTimeoutRef.current) {
        clearTimeout(turnLockTimeoutRef.current)
        turnLockTimeoutRef.current = null
      }
    }
  }, [turnLock, myUid, setTurnLockBroadcast])

  //  dono do cadeado de turno (garante que s o iniciador destrava)
  //  CORREO: Declarado ANTES do useEffect que o usa
  const [lockOwner, setLockOwner] = React.useState(null)
  const lockOwnerRef = React.useRef(null)
  React.useEffect(() => { lockOwnerRef.current = lockOwner }, [lockOwner])

  //  dados do prximo turno (para evitar stale closure)
  //  CORREO: Declarado ANTES do useEffect que o usa
  const pendingTurnDataRef = React.useRef(null)
  
  //  CORREO CRTICA: Ref para pegar valor atualizado de round (evita stale closure)
  const currentRoundRef = React.useRef(round)
  React.useEffect(() => { 
    currentRoundRef.current = round
    console.log('[DEBUG]  currentRoundRef atualizado para:', round)
  }, [round])

  //  refs do estado mais recente (evita stale no tick / winner)
  const playersRef = React.useRef(players)
  React.useEffect(() => { playersRef.current = players }, [players])

  const turnIdxRef = React.useRef(turnIdx)
  React.useEffect(() => { turnIdxRef.current = turnIdx }, [turnIdx])

  const gameOverRef = React.useRef(gameOver)
  React.useEffect(() => { gameOverRef.current = gameOver }, [gameOver])

  //  ENDGAME: pendente + idempotncia
  const endGamePendingRef = React.useRef(false)
  const endGameFinalizedRef = React.useRef(false)

  //  CORREO: Atualiza lockOwner quando turnIdx muda (incluindo via SYNC)
  React.useEffect(() => {
    const currentPlayer = players[turnIdx]
    if (currentPlayer && String(currentPlayer.id) === String(myUid)) {
      // Se  minha vez, atualiza lockOwner para permitir que eu mude o turno
      console.log('[DEBUG]  minha vez - atualizando lockOwner para:', myUid, 'turnIdx:', turnIdx)
      setLockOwner(String(myUid))
      //  CORREO: NO limpa pendingTurnDataRef aqui se h dados pendentes para um turno futuro
      // O tick precisa usar esses dados para mudar o turno
      // S limpa se os dados pendentes so para o turno atual (j foi processado)
      if (pendingTurnDataRef.current) {
        if (pendingTurnDataRef.current.nextTurnIdx === turnIdx) {
          // Se os dados pendentes so para o turno atual, limpa (j foi processado)
          console.log('[DEBUG] Limpando pendingTurnDataRef - turno j foi processado (nextTurnIdx === turnIdx)')
          pendingTurnDataRef.current = null
        } else {
          // Se os dados pendentes so para um turno futuro, mantm (tick ainda precisa usar)
          console.log('[DEBUG] Mantendo pendingTurnDataRef - prximo turno:', pendingTurnDataRef.current.nextTurnIdx, 'turno atual:', turnIdx)
        }
      }
    } else {
      // Se no  minha vez, limpa lockOwner e pendingTurnDataRef
      if (lockOwner === String(myUid)) {
        console.log('[DEBUG] No  mais minha vez - limpando lockOwner')
        setLockOwner(null)
      }
      //  CORREO: S limpa pendingTurnDataRef se no h dados pendentes para o prximo turno
      // (pode ser que o turno esteja mudando e o tick ainda precise dos dados)
      if (pendingTurnDataRef.current && pendingTurnDataRef.current.nextTurnIdx !== turnIdx) {
        // Se os dados pendentes no so para o turno atual, pode limpar
        console.log('[DEBUG] Limpando pendingTurnDataRef - no  minha vez e dados no so para turno atual')
        pendingTurnDataRef.current = null
      }
    }
  }, [turnIdx, players, myUid, lockOwner])

  // helper: abrir modal e "travar"/"destravar" o contador
  const openModalAndWait = async (element) => {
    if (!(pushModal && awaitTop)) return null
    
    //  CORREO: Marca que uma modal est sendo aberta ANTES de qualquer coisa
    openingModalRef.current = true
    
    //  CORREO: Usa funo de atualizao para garantir que sempre pega o valor mais recente
    let currentLockCount = 0
    setModalLocks(prev => {
      currentLockCount = prev
      const newLockCount = prev + 1
      modalLocksRef.current = newLockCount
      lastModalClosedTimeRef.current = null //  CORREO: Reseta timestamp quando abre modal
      console.log('[DEBUG] openModalAndWait - ABRINDO modal, modalLocks:', prev, '->', newLockCount, 'openingModalRef:', openingModalRef.current)
      return newLockCount
    })
    
    let modalResolved = false
    try {
      pushModal(element)
      //  CORREO: Pequeno delay para garantir que a modal foi renderizada
      await new Promise(resolve => setTimeout(resolve, 100))
      openingModalRef.current = false
      console.log('[DEBUG] openModalAndWait - Modal renderizada, openingModalRef:', openingModalRef.current)
      const res = await awaitTop()
      modalResolved = true
      //  CORREO: Pequeno delay aps resolver para garantir que a modal foi completamente fechada
      await new Promise(resolve => setTimeout(resolve, 50))
      return res
    } catch (error) {
      console.error('[DEBUG]  ERRO ao abrir/aguardar modal:', error)
      modalResolved = true
      //  CORREO: Em caso de erro, garante que o contador seja decrementado
      openingModalRef.current = false
      setModalLocks(prev => {
        const newLockCountAfter = Math.max(0, prev - 1)
        modalLocksRef.current = newLockCountAfter
        //  CORREO: Se fechou a ltima modal, marca o timestamp
        if (newLockCountAfter === 0) {
          lastModalClosedTimeRef.current = Date.now()
        }
        console.log('[DEBUG] openModalAndWait - ERRO, decrementando modalLocks:', prev, '->', newLockCountAfter)
        return newLockCountAfter
      })
      return null
    } finally {
      //  CORREO: Garante que openingModalRef seja sempre resetado
      if (!modalResolved) {
        openingModalRef.current = false
      }
      //  CORREO: Usa funo de atualizao para garantir que sempre pega o valor mais recente
      // S decrementa se a modal foi realmente resolvida (no decrementa duas vezes)
      if (modalResolved) {
        setModalLocks(prev => {
          const newLockCountAfter = Math.max(0, prev - 1)
          modalLocksRef.current = newLockCountAfter
          //  CORREO: Se fechou a ltima modal, marca o timestamp
          if (newLockCountAfter === 0) {
            lastModalClosedTimeRef.current = Date.now()
            console.log('[DEBUG] openModalAndWait - LTIMA MODAL FECHADA - timestamp:', lastModalClosedTimeRef.current)
          }
          console.log('[DEBUG] openModalAndWait - FECHANDO modal, modalLocks:', prev, '->', newLockCountAfter)
          return newLockCountAfter
        })
      }
    }
  }


  // ========= regras auxiliares de saldo =========
  const canPay = React.useCallback((idx, amount) => {
    const p = players[idx]
    const amt = Math.max(0, Number(amount || 0))
    return (Number(p?.cash || 0) >= amt)
  }, [players])

  const requireFunds = React.useCallback((idx, amount, reason) => {
    const ok = canPay(idx, amount)
    if (!ok) {
      appendLog(`Saldo insuficiente${reason ? ' para ' + reason : ''}. Use RECUPERAO (demitir / emprestar / reduzir) ou declare FALNCIA.`)
    }
    return ok
  }, [canPay, appendLog])

  // ========= fim de jogo =========
  const maybeFinishGame = React.useCallback((finalPlayers, nextRound, finalTurnIdx) => {
    // nextRound = 6 significa: a 5 rodada terminou agora
    if (nextRound <= 5) return false

    const alivePlayers = (finalPlayers || []).filter(p => !p?.bankrupt)

    // caso extremo: ningum vivo
    if (alivePlayers.length === 0) {
      console.log('[DEBUG]  FIM DE JOGO - Nenhum jogador vivo restante')
      setPlayers(finalPlayers)
      setWinner(null)
      setGameOver(true)
      setRound(5)
      currentRoundRef.current = 5
      appendLog('Fim de jogo! Todos os jogadores falidos.')
      pendingTurnDataRef.current = null
      setTurnLockBroadcast(false)

      try {
        broadcastState(finalPlayers || [], finalTurnIdx || turnIdx, 5, true, null, { round: 5, gameOver: true, winner: null })
      } catch {}
      return true
    }

    // vencedor: maior patrimnio (cash + bens). Desempate: cash, depois nome.
    const ranked = alivePlayers
      .map(p => ({
        player: p,
        patrimonio: (p.cash || 0) + (p.bens || 0),
        cash: (p.cash || 0),
      }))
      .sort((a, b) =>
        (b.patrimonio - a.patrimonio) ||
        (b.cash - a.cash) ||
        String(a.player?.name || '').localeCompare(String(b.player?.name || ''))
      )

    const champ = ranked[0]?.player || null

    console.log("[ENDGAME] finalizando: vencedor=", champ?.name || "N/A", ", round=5")

    setPlayers(finalPlayers)
    setWinner(champ)
    setGameOver(true)
    setRound(5)
    currentRoundRef.current = 5
    appendLog(`Fim de jogo! 5 rodadas completas. Vencedor: ${champ?.name || ''}`)
    pendingTurnDataRef.current = null
    setTurnLockBroadcast(false)

    //  propaga fim do jogo para todos os clientes
    try {
      broadcastState(finalPlayers || [], finalTurnIdx || turnIdx, 5, true, champ, { round: 5, gameOver: true, winner: champ })
    } catch {}

    return true
  }, [appendLog, broadcastState, setGameOver, setRound, setTurnLockBroadcast, setWinner, turnIdx])

  // ========= ao de andar no tabuleiro (inclui TODA a lgica de casas/modais) =========
  const advanceAndMaybeLap = React.useCallback((steps, deltaCash, note) => {
    console.log('[DEBUG]  advanceAndMaybeLap chamada - steps:', steps, 'deltaCash:', deltaCash, 'note:', note)
    if (gameOverRef.current || endGamePendingRef.current || endGameFinalizedRef.current || !players.length) return

    //  CORREO: Verifica se j h uma mudana de turno em progresso
    if (turnChangeInProgressRef.current) {
      console.warn('[DEBUG]  advanceAndMaybeLap - mudana de turno j em progresso, ignorando')
      return
    }

    //  CORREO: Verifica se h modais abertas antes de iniciar
    if (modalLocksRef.current > 0 || openingModalRef.current) {
      console.warn('[DEBUG]  advanceAndMaybeLap - h modais abertas, aguardando...', {
        modalLocks: modalLocksRef.current,
        opening: openingModalRef.current
      })
      // Aguarda um pouco e tenta novamente
      setTimeout(() => {
        if (modalLocksRef.current === 0 && !openingModalRef.current) {
          advanceAndMaybeLap(steps, deltaCash, note)
        }
      }, 200)
      return
    }

    //  BUG 2 FIX: try/finally para garantir liberao de turnLock em caso de erro
    // Bloqueia os prximos jogadores at esta ao (e todas as modais) terminar
    turnChangeInProgressRef.current = true
    setTurnLockBroadcast(true)
    setLockOwner(String(myUid))
    
    try {
    //  CORREO: Usa playersOrdered para garantir ordem consistente e obtm ID do jogador da vez
    const curIdx = turnIdx
    const cur = playersOrdered[curIdx]
    if (!cur) { 
      setTurnLockBroadcast(false)
      turnChangeInProgressRef.current = false
      return 
    }
    
    //  CORREO: Obtm ID do jogador da vez para atualizaes por ID (no por ndice)
    const ownerId = String(cur.id)
    
    console.log('[DEBUG]  POSIO INICIAL - Jogador:', cur.name, 'Posio:', cur.pos, 'Saldo:', cur.cash)

    // ========= funo recursiva para lidar com saldo insuficiente =========
    const handleInsufficientFunds = async (requiredAmount, context, action, currentPlayers = players) => {
      const currentCash = Number(currentPlayers[curIdx]?.cash || 0)
      
      if (currentCash >= requiredAmount) {
        // Processa o pagamento j que tem saldo suficiente
        console.log('[DEBUG]  Saldo suficiente! Processando pagamento de:', requiredAmount)
        //  CORREO: Atualiza jogador por ID, no por ndice
        const updatedPlayers = normalizePlayers(currentPlayers).map((p) => 
          String(p.id) !== ownerId ? p : { ...p, cash: Math.max(0, (p.cash || 0) - requiredAmount), pos: p.pos }
        )
        setPlayers(updatedPlayers)
        broadcastState(updatedPlayers, turnIdx, currentRoundRef.current)
        return true // Tem saldo suficiente e pagou
      }

      //  CORREO: Marca que uma modal ser aberta ANTES de abrir
      openingModalRef.current = true
      // Mostra modal de saldo insuficiente
      const recoveryRes = await openModalAndWait(
        <InsufficientFundsModal
          requiredAmount={requiredAmount}
          currentCash={currentCash}
          title={`Saldo insuficiente para ${action} ${context}`}
          message={`Voc precisa ${action} R$ ${requiredAmount.toLocaleString()} mas possui apenas R$ ${currentCash.toLocaleString()}.`}
          showRecoveryOptions={true}
        />
      )
      
      if (!recoveryRes) {
        setTurnLockBroadcast(false)
        return false
      }
      
      if (recoveryRes.action === 'RECOVERY') {
        // Abre modal de recuperao financeira (no pode ser fechada)
        console.log('[DEBUG] Abrindo RecoveryModal para jogador:', currentPlayers[curIdx])
        const recoveryModalRes = await openModalAndWait(<RecoveryModal currentPlayer={currentPlayers[curIdx]} canClose={false} />)
        console.log('[DEBUG] RecoveryModal retornou:', recoveryModalRes)
        if (recoveryModalRes) {
          // Processa a ao de recuperao
          console.log('[DEBUG] recoveryModalRes existe, tipo:', recoveryModalRes.type, 'action:', recoveryModalRes.action)
          let updatedPlayers = currentPlayers
          
          if (recoveryModalRes.type === 'FIRE') {
            console.log('[DEBUG]  Condio FIRE atendida! Processando demisses:', recoveryModalRes)
            const deltas = {
              cashDelta: Number(recoveryModalRes.amount || 0),
              vendedoresComunsDelta: -Number(recoveryModalRes.items?.comum || 0),
              fieldSalesDelta: -Number(recoveryModalRes.items?.field || 0),
              insideSalesDelta: -Number(recoveryModalRes.items?.inside || 0),
              gestoresDelta: -Number(recoveryModalRes.items?.gestor || 0),
            }
            console.log('[DEBUG] Deltas de demisso:', deltas)
            //  CORREO: Preserva a posio do jogador ao atualizar
            updatedPlayers = currentPlayers.map((p, i) => {
              if (i !== curIdx) return p
              const updated = applyDeltas(p, deltas)
              // Preserva a posio original
              return { ...updated, pos: p.pos }
            })
            console.log('[DEBUG] Novo saldo aps demisses:', updatedPlayers[curIdx]?.cash)
            setPlayers(updatedPlayers)
            broadcastState(updatedPlayers, turnIdx, currentRoundRef.current)
          } else if (recoveryModalRes.type === 'LOAN') {
            console.log('[DEBUG]  Condio LOAN atendida! Processando emprstimo:', recoveryModalRes)
            
            // Verifica se o jogador j tem um emprstimo pendente
            const currentLoan = currentPlayers[curIdx]?.loanPending
            if (currentLoan && Number(currentLoan.amount) > 0) {
              console.log('[DEBUG]  Jogador j possui emprstimo pendente:', currentLoan)
              // Mostra modal informando que j tem emprstimo - NO PODE FECHAR
              const loanModalRes = await openModalAndWait(
                <InsufficientFundsModal
                  requiredAmount={requiredAmount}
                  currentCash={currentPlayers[curIdx]?.cash || 0}
                  title="Emprstimo j realizado"
                  message={`Voc j possui um emprstimo pendente de R$ ${Number(currentLoan.amount).toLocaleString()}. Cada jogador s pode ter um emprstimo por vez.`}
                  showRecoveryOptions={false}
                  canClose={false} // NO PODE FECHAR
                />
              )
              // Fora o jogador a declarar falncia se j tem emprstimo
              if (!loanModalRes || loanModalRes.action !== 'BANKRUPT') {
                setTurnLockBroadcast(false)
                return false
              }
              // Processa falncia
              const updatedPlayers = currentPlayers.map((p, i) => (i === curIdx ? { ...p, bankrupt: true } : p))
              const alive = countAlivePlayers(updatedPlayers)
              if (alive <= 1) {
                const winnerIdx = updatedPlayers.findIndex(p => !p?.bankrupt)
                setWinner(winnerIdx >= 0 ? updatedPlayers[winnerIdx] : null)
                setPlayers(updatedPlayers)
                setGameOver(true)
                setTurnLockBroadcast(false)
                broadcastState(updatedPlayers, turnIdx, round, true, winnerIdx >= 0 ? updatedPlayers[winnerIdx] : null)
                return false
              }
              const nextIdx = findNextAliveIdx(updatedPlayers, curIdx)
              //  CORREO MULTIPLAYER: Calcula turnPlayerId do prximo jogador
              const nextPlayer = updatedPlayers[nextIdx]
              const nextTurnPlayerId = nextPlayer?.id ? String(nextPlayer.id) : null
              setPlayers(updatedPlayers)
              setTurnIdx(nextIdx)
              setTurnLockBroadcast(false)
              broadcastState(updatedPlayers, nextIdx, currentRoundRef.current, false, null, {
                turnPlayerId: nextTurnPlayerId //  CORREO: turnPlayerId autoritativo
              })
              return false
            }
            
            const amt = Number(recoveryModalRes.amount || 0)
            console.log('[DEBUG] Valor do emprstimo:', amt)
            console.log('[DEBUG] Saldo atual do jogador:', currentPlayers[curIdx]?.cash)
            //  CORREO: Preserva a posio do jogador ao atualizar
            //  CORREO: Emprstimo ser cobrado na prxima vez que passar pela casa de despesas operacionais
            // No usa dueRound baseado em rodada, mas sim uma flag para indicar que deve ser cobrado na prxima passagem
            updatedPlayers = currentPlayers.map((p, i) =>
              i !== curIdx ? p : {
                ...p,
                cash: (Number(p.cash) || 0) + amt,
                loanPending: { 
                  amount: amt, 
                  charged: false,
                  //  CORREO: Marca que o emprstimo deve ser cobrado na prxima passagem pela casa de despesas
                  shouldChargeOnNextExpenses: true
                },
                pos: p.pos //  CORREO: Preserva a posio
              }
            )
            console.log('[DEBUG] Novo saldo do jogador:', updatedPlayers[curIdx]?.cash)
            console.log('[DEBUG] Novo loanPending:', updatedPlayers[curIdx]?.loanPending)
            setPlayers(updatedPlayers)
            broadcastState(updatedPlayers, turnIdx, currentRoundRef.current)
          } else if (recoveryModalRes.type === 'REDUCE') {
            console.log('[DEBUG]  Condio REDUCE atendida! Processando reduo:', recoveryModalRes)
            const selections = recoveryModalRes.items || []
            let totalCredit = 0
            console.log('[DEBUG] Selees para reduzir:', selections)
            //  CORREO: Preserva a posio do jogador ao atualizar
            updatedPlayers = currentPlayers.map((p, i) => {
              if (i !== curIdx) return p
              let next = { ...p }
              for (const sel of selections) {
                if (sel.selected) {
                  totalCredit += Number(sel.credit || 0)
                  if (sel.group === 'MIX') {
                    next.mixOwned = { ...(next.mixOwned || {}), [sel.level]: false }
                  } else if (sel.group === 'ERP') {
                    next.erpOwned = { ...(next.erpOwned || {}), [sel.level]: false }
                  }
                }
              }
              next.cash = (Number(next.cash) || 0) + totalCredit
              //  CORREO: Preserva a posio original
              next.pos = p.pos
              return next
            })
            console.log('[DEBUG] Total de crdito da reduo:', totalCredit)
            console.log('[DEBUG] Novo saldo aps reduo:', updatedPlayers[curIdx]?.cash)
            setPlayers(updatedPlayers)
            broadcastState(updatedPlayers, turnIdx, currentRoundRef.current)
          } else {
            console.log('[DEBUG]  Nenhuma condio foi atendida! Tipo:', recoveryModalRes.type, 'Action:', recoveryModalRes.action)
          }
          
          // Verifica se agora tem saldo suficiente aps a recuperao
          const newCash = Number(updatedPlayers[curIdx]?.cash || 0)
          console.log('[DEBUG] Verificando saldo aps recuperao - Novo saldo:', newCash, 'Necessrio:', requiredAmount)
          
          if (newCash >= requiredAmount) {
            console.log('[DEBUG]  Saldo suficiente aps recuperao! Processando pagamento de:', requiredAmount)
            // Processa o pagamento j que tem saldo suficiente
            //  CORREO: Preserva a posio do jogador ao atualizar
            const finalPlayers = updatedPlayers.map((p, i) => 
              i !== curIdx ? p : { ...p, cash: Math.max(0, (p.cash || 0) - requiredAmount), pos: p.pos }
            )
            console.log('[DEBUG]  PAGAMENTO - Saldo antes:', updatedPlayers[curIdx]?.cash, 'Valor a pagar:', requiredAmount, 'Saldo aps:', finalPlayers[curIdx]?.cash)
            setPlayers(finalPlayers)
            broadcastState(finalPlayers, turnIdx, currentRoundRef.current)
            return true
          } else {
            console.log('[DEBUG]  Saldo ainda insuficiente aps recuperao. Continuando recurso...')
            // Recursivamente verifica se agora tem saldo suficiente com o estado atualizado
            return await handleInsufficientFunds(requiredAmount, context, action, updatedPlayers)
          }
        } else {
          setTurnLockBroadcast(false)
          return false
        }
      } else if (recoveryRes.action === 'BANKRUPT') {
        // Processa falncia
        const updatedPlayers = currentPlayers.map((p, i) => (i === curIdx ? { ...p, bankrupt: true } : p))
        const alive = countAlivePlayers(updatedPlayers)
        if (alive <= 1) {
          const winnerIdx = updatedPlayers.findIndex(p => !p?.bankrupt)
          setWinner(winnerIdx >= 0 ? updatedPlayers[winnerIdx] : null)
          setPlayers(updatedPlayers)
          setGameOver(true)
          setTurnLockBroadcast(false)
          broadcastState(updatedPlayers, turnIdx, round, true, winnerIdx >= 0 ? updatedPlayers[winnerIdx] : null)
          return false
        }
        const nextIdx = findNextAliveIdx(updatedPlayers, curIdx)
        //  CORREO MULTIPLAYER: Calcula turnPlayerId do prximo jogador
        const nextPlayer = updatedPlayers[nextIdx]
        const nextTurnPlayerId = nextPlayer?.id ? String(nextPlayer.id) : null
        setPlayers(updatedPlayers)
        setTurnIdx(nextIdx)
        setTurnLockBroadcast(false)
        broadcastState(updatedPlayers, nextIdx, currentRoundRef.current, false, null, {
          turnPlayerId: nextTurnPlayerId //  CORREO: turnPlayerId autoritativo
        })
        return false
      } else {
        setTurnLockBroadcast(false)
        return false
      }
    }

    const oldPos = cur.pos
    const newPos = (oldPos + steps) % TRACK_LEN
    const lap = newPos < oldPos
    
    //  CORREO CRTICA: Detecta volta completa e incrementa rodada individual do jogador
    // NOTA: A rodada geral s incrementa quando TODOS passam pela casa 0, mas aqui detectamos volta completa
    const completedLap = lap && oldPos >= TRACK_LEN - 1  // Se estava na ltima casa e deu volta

    console.log('[DEBUG]  MOVIMENTO - De posio:', oldPos, 'Para posio:', newPos, 'Steps:', steps, 'Lap:', lap, 'CompletedLap:', completedLap)

    //  CORREO: Verifica se passou pela casa 0 (Faturamento do Ms)
    const crossedStart1ForRound = crossedTile(oldPos, newPos, 0) || lap
    
    // aplica movimento + eventual cashDelta imediato (sem permitir negativo)
    //  Adiciona lastRevenueRound e waitingAtRevenue
    const roundNow = currentRoundRef.current
    const aliveCount = players.filter(p => !p?.bankrupt).length
    
    //  CORREO: Atualiza jogador por ID/seat, no por ndice
    const nextPlayers = normalizePlayers(players).map((p) => {
      if (String(p.id) !== ownerId) return p
      const nextCash = (p.cash || 0) + (deltaCash || 0)
      
      // Inicializa campos se no existirem
      const lastRevenueRound = Number(p.lastRevenueRound) || 0
      let waitingAtRevenue = false
      let finalPos = newPos
      
      // Se cruzou o faturamento, atualiza lastRevenueRound
      if (crossedStart1ForRound) {
        const newLastRevenueRound = Math.max(lastRevenueRound, roundNow)
        
        // Trava somente na 5 rodada se h mais de 1 jogador vivo
        if (roundNow === 5 && aliveCount > 1) {
          waitingAtRevenue = true
          finalPos = 0  // Trava na casa 0
        }
        
        return {
          ...p,
          pos: finalPos,
          cash: Math.max(0, nextCash),
          lastRevenueRound: newLastRevenueRound,
          waitingAtRevenue
        }
      }
      
      return {
        ...p,
        pos: finalPos,
        cash: Math.max(0, nextCash),
        lastRevenueRound,
        waitingAtRevenue: false  // Nunca trava em rodadas < 5
      }
    })
    
    //  CORREO: Encontra jogador atualizado por ID para log
    const updatedCur = nextPlayers.find(p => String(p.id) === ownerId)
    console.log('[DEBUG]  APS MOVIMENTO - Jogador:', updatedCur?.name, 'Posio:', updatedCur?.pos, 'Saldo:', updatedCur?.cash, 'lastRevenueRound:', updatedCur?.lastRevenueRound, 'waitingAtRevenue:', updatedCur?.waitingAtRevenue)

    // >>> controle de rodada: s vira quando TODOS os jogadores VIVOS cruzarem a casa 0
    //  CORREO CRTICA: Usa ref para pegar valor atualizado de round (evita stale closure)
    let nextRound = currentRoundRef.current
    let nextFlags = [...roundFlags]
    let shouldIncrementRound = false
    let shouldEndGameAfterTick = false
    
    //  CORREO CRTICA: Se jogador completou volta completa (lap), marca flag imediatamente
    // Isso garante que a flag seja marcada mesmo se crossedStart1ForRound no detectar corretamente
    if (lap) {
      console.log('[DEBUG]  VOLTA COMPLETA DETECTADA (lap=true) - Jogador completou uma volta completa!', {
        jogador: cur.name,
        posicaoAnterior: oldPos,
        posicaoNova: newPos,
        rodadaAtual: currentRoundRef.current
      })
      
      // Garante que o array de flags tem o tamanho correto
      if (nextFlags.length < players.length) {
        nextFlags = [...nextFlags, ...new Array(players.length - nextFlags.length).fill(false)]
      }
      
      //  CORREO: Encontra ndice do jogador atual por ID para marcar flag
      const curIdxForFlag = playersOrdered.findIndex(p => String(p.id) === ownerId)
      if (curIdxForFlag >= 0) {
        nextFlags[curIdxForFlag] = true
      }
      console.log('[DEBUG]  Flag marcada por volta completa (lap) - Flags:', nextFlags.map((f, i) => `${playersOrdered[i]?.name}:${f}`).join(', '))
    }
    
    //  CORREO: Usa crossedStart1ForRound para detectar passagem pela casa 0 (pode ser sem dar volta completa)
    // Se lap j marcou a flag, isso garante que no perde a marcao
    if (crossedStart1ForRound) {
      // Garante que o array de flags tem o tamanho correto
      if (nextFlags.length < players.length) {
        nextFlags = [...nextFlags, ...new Array(players.length - nextFlags.length).fill(false)]
      }
      
      //  CORREO: Encontra ndice do jogador atual por ID para marcar flag
      const curIdxForFlag = playersOrdered.findIndex(p => String(p.id) === ownerId)
      if (curIdxForFlag >= 0) {
        if (!nextFlags[curIdxForFlag]) {
          nextFlags[curIdxForFlag] = true
          console.log('[DEBUG]  Jogador passou pela casa 0 (crossedStart1ForRound) - Flags:', nextFlags.map((f, i) => `${playersOrdered[i]?.name}:${f}`).join(', '))
        } else {
          console.log('[DEBUG]  Jogador j tinha flag marcada (por lap) - Flags:', nextFlags.map((f, i) => `${playersOrdered[i]?.name}:${f}`).join(', '))
        }
      }
      
      //  CORREO: Conta apenas jogadores vivos para verificar se todos passaram
      const alivePlayers = nextPlayers.filter(p => !p?.bankrupt)
      const aliveIndices = nextPlayers.map((p, i) => !p?.bankrupt ? i : -1).filter(i => i >= 0)
      
      //  CORREO: Verifica se todos os jogadores vivos passaram pela casa 0
      // Usa lastRevenueRound >= currentRoundRef.current (robusto)
      const alive = nextPlayers.filter(p => !p?.bankrupt)
      const allAliveDone = alive.length > 0 && alive.every(p => (Number(p.lastRevenueRound) || 0) >= currentRoundRef.current)
      
      console.log('[DEBUG]  Verificando allAliveDone - Rodada atual:', currentRoundRef.current, 'Jogadores vivos:', alive.map(p => ({ name: p.name, lastRevenueRound: p.lastRevenueRound })))
      
      const curForLog = nextPlayers.find(p => String(p.id) === ownerId)
      console.log('[DEBUG]  Verificao de rodada - Jogador:', curForLog?.name, 'Rodada atual:', currentRoundRef.current, 'round do closure:', round)
      console.log('[DEBUG]  Jogadores vivos:', aliveIndices.map(i => {
        const p = nextPlayers[i]
        const flagIdx = playersOrdered.findIndex(po => String(po.id) === String(p?.id))
        return `${p?.name}:${flagIdx >= 0 ? nextFlags[flagIdx] : false}`
      }).join(', '))
      console.log('[DEBUG]  Total de jogadores vivos:', aliveIndices.length)
      console.log('[DEBUG]  Flags completas:', nextFlags.map((f, i) => `${nextPlayers[i]?.name}:${f ? '' : ''}`).join(', '))
      console.log('[DEBUG]  Todos passaram pela casa 0?', allAliveDone)
      
      if (allAliveDone) {
        //  CORREO CRTICA: Usa valor atualizado de round via ref, no do closure
        const currentRoundValue = currentRoundRef.current
        const computedNextRound = currentRoundValue + 1
        
        nextRound = computedNextRound
        shouldIncrementRound = true
        
        //  FIM DE JOGO: quando round===5 e shouldIncrementRound, finaliza
        if (currentRoundValue === 5 && shouldIncrementRound) {
          console.log("[ENDGAME] detectado: fim da 5 (tentaria round=6)");
          maybeFinishGame(nextPlayers, 6, turnIdx);
          return;
        }
        
        console.log('[DEBUG]  Calculando incremento - round (ref):', currentRoundValue, 'round (closure):', round, 'nextRound:', nextRound)
        
        //  CORREO: Reseta apenas as flags dos jogadores vivos (mantm flags de falidos)
        nextFlags = nextFlags.map((_, idx) => {
          if (nextPlayers[idx]?.bankrupt) {
            // Mantm a flag do jogador falido (no reseta)
            return nextFlags[idx]
          } else {
            // Reseta a flag do jogador vivo
            return false
          }
        })
        console.log('[DEBUG]  RODADA INCREMENTADA - Nova rodada:', nextRound, 'Rodada anterior (ref):', currentRoundValue, 'Rodada anterior (closure):', round, 'Jogadores vivos:', alive.length)
        console.log('[DEBUG]  Flags resetadas:', nextFlags.map((f, i) => `${nextPlayers[i]?.name}:${f}`).join(', '))
        appendLog(` Rodada ${nextRound} iniciada! Todos os jogadores vivos passaram pela casa de faturamento.`)
      } else {
        const missingPlayers = alive.filter(p => (Number(p.lastRevenueRound) || 0) < currentRoundRef.current).map(p => p.name)
        console.log('[DEBUG]  Rodada NO incrementada - ainda faltam jogadores completarem o ms:', missingPlayers.join(', '))
      }
    }
    
    //  CORREO: Atualiza as flags ANTES de atualizar a rodada
    setRoundFlags(nextFlags)
    console.log('[DEBUG] roundFlags updated / round closed:', nextFlags.map((f, i) => `${nextPlayers[i]?.name}:${f}`).join(', '))
    
    // >>> pular jogadores falidos ao decidir o prximo turno
    //  Na rodada 5, pula tambm quem est waitingAtRevenue
    let nextTurnIdx = findNextAliveIdx(nextPlayers, curIdx)
    
    // Pula waiting na rodada 5
    if (currentRoundRef.current === 5) {
      let guard = 0
      while (guard < nextPlayers.length) {
        const p = nextPlayers[nextTurnIdx]
        if (!p.bankrupt && !(p.waitingAtRevenue === true)) break
        nextTurnIdx = (nextTurnIdx + 1) % nextPlayers.length
        guard++
      }
    }
    
    if (deltaCash) appendLog(`${cur.name} ${deltaCash>0? 'ganhou' : 'pagou'} $${(Math.abs(deltaCash)).toLocaleString()}`)
    if (note) appendLog(note)
    
    setPlayers(nextPlayers)
    
    //  CORREO CRTICA: Atualiza a rodada garantindo que o incremento acontea corretamente
    // Usa funo de atualizao para sempre pegar o valor mais recente do estado
    // Se shouldIncrementRound  true, fora o incremento mesmo se houver sincronizao
    setRound(prevRound => {
      let finalRound = prevRound
      
      //  CORREO: Se todos passaram pela casa 0, incrementa a rodada
      if (shouldIncrementRound) {
        // Se nextRound foi calculado corretamente e  maior que prevRound, usa nextRound
        if (nextRound > prevRound) {
          finalRound = nextRound
          console.log('[DEBUG]  RODADA INCREMENTADA - Rodada anterior:', prevRound, 'Nova rodada:', finalRound, 'nextRound calculado:', nextRound)
          console.log('[DEBUG]  Rodada incrementada com sucesso!')
          appendLog(` Rodada ${finalRound} iniciada! Todos os jogadores vivos passaram pela casa de faturamento.`)
        } else {
          // Se nextRound no  maior (pode ter sido calculado com round desatualizado), incrementa manualmente
          finalRound = prevRound + 1
          console.log('[DEBUG]  RODADA INCREMENTADA (forado) - Rodada anterior:', prevRound, 'Nova rodada:', finalRound, 'nextRound calculado:', nextRound)
          console.log('[DEBUG]  Rodada incrementada com sucesso (forado devido a sincronizao)!')
          appendLog(` Rodada ${finalRound} iniciada! Todos os jogadores vivos passaram pela casa de faturamento.`)
        }
      } else {
        // Se no deve incrementar, usa Math.max para proteger contra reverso
        finalRound = Math.max(nextRound, prevRound)
        if (finalRound !== prevRound) {
          console.log('[DEBUG]  Rodada atualizada via Math.max - prevRound:', prevRound, 'nextRound:', nextRound, 'finalRound:', finalRound)
        }
      }
      
      //  CORREO: Atualiza o ref com o valor final para uso futuro
      currentRoundRef.current = finalRound
      
      return finalRound
    })
    
    //  CORREO: Armazena os dados do prximo turno para uso na funo tick
    // IMPORTANTE: No atualiza turnIdx ainda - isso ser feito pelo tick quando todas as modais fecharem
    // IMPORTANTE: Usa nextRound calculado acima (pode ser diferente de round se todos passaram pela casa 0)
    //  FIX: pendingTurnDataRef deve carregar EXATAMENTE a rodada calculada
    // (nunca somar +1 aqui, seno o tick/broadcast sai com round errado)
    const finalNextRound = nextRound
    const finalNextFlags = nextFlags
    //  CORREO MULTIPLAYER: Calcula turnPlayerId do prximo jogador (fonte autoritativa)
    const nextPlayer = nextPlayers[nextTurnIdx]
    const nextTurnPlayerId = nextPlayer?.id ? String(nextPlayer.id) : null
    pendingTurnDataRef.current = {
      nextPlayers,
      nextTurnIdx,
      nextTurnPlayerId, //  CORREO: turnPlayerId do prximo jogador
      nextRound: finalNextRound,
      nextRoundFlags: finalNextFlags,
      timestamp: Date.now(),
      shouldIncrementRound,
      endGame: shouldEndGameAfterTick
    }
    console.log('[DEBUG]  pendingTurnDataRef preenchido - prximo turno:', nextTurnIdx, 'rodada atual:', round, 'prxima rodada:', finalNextRound, 'nextRound calculado:', nextRound, 'shouldIncrementRound:', shouldIncrementRound, 'rodada foi incrementada?', nextRound > round, 'roundFlags:', finalNextFlags.map((f, i) => `${nextPlayers[i]?.name}:${f}`).join(', '))
    
    // NO muda o turno aqui - aguarda todas as modais serem fechadas
    // O turno ser mudado na funo tick() quando modalLocks === 0

    //  Fim de jogo por rodada (correto):
    // Se a prxima rodada calculada for 6, significa que a 5 rodada terminou agora.
    if (maybeFinishGame(nextPlayers, nextRound, turnIdx)) {
      // evita deixar o motor "travado" esperando tick
      pendingTurnDataRef.current = null
      turnChangeInProgressRef.current = false
      openingModalRef.current = false
      return
    }

    const landedOneBased = newPos + 1
    const crossedStart1 = crossedTile(oldPos, newPos, 0)
    const crossedExpenses23 = crossedTile(oldPos, newPos, 22)

    // ================== Regras por casas (modais) ==================

    //  CORREO: Flag para indicar que uma modal ser aberta (setada antes de abrir)
    let willOpenModal = false

    // ERP
    const isErpTile = (landedOneBased === 6 || landedOneBased === 16 || landedOneBased === 32 || landedOneBased === 49)
    if (isErpTile && isMyTurn && pushModal && awaitTop) {
      willOpenModal = true
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      ;(async () => {
        const currentErpLevel = players[curIdx]?.erpLevel || null
        const erpOwned = players[curIdx]?.erpOwned || players[curIdx]?.erp || {}
        const res = await openModalAndWait(<ERPSystemsModal 
          currentCash={nextPlayers[curIdx]?.cash ?? myCash}
          currentLevel={currentErpLevel}
          erpOwned={erpOwned}
        />)
        if (!res || res.action !== 'BUY') return
        const price = Number(res.values?.compra || 0)
        if (!requireFunds(curIdx, price, 'comprar ERP')) { setTurnLockBroadcast(false); return }
        setPlayers(ps => {
          const upd = ps.map((p, i) =>
            i !== curIdx ? p : applyDeltas(p, { cashDelta: -price, erpLevelSet: res.level })
          )
          //  CORREO: Usa turnIdx e round atuais (no nextTurnIdx/nextRound) para compras durante o turno
          broadcastState(upd, turnIdx, currentRoundRef.current)
          return upd
        })
      })()
    }

    // Treinamento
    const isTrainingTile = (landedOneBased === 2 || landedOneBased === 11 || landedOneBased === 19 || landedOneBased === 47)
    if (isTrainingTile && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      ;(async () => {
        const ownerForTraining = players.find(isMine) || nextPlayers[curIdx]
        const res = await openModalAndWait(<TrainingModal
          canTrain={{
            comum:  Number(ownerForTraining?.vendedoresComuns) || 0,
            field:  Number(ownerForTraining?.fieldSales) || 0,
            inside: Number(ownerForTraining?.insideSales) || 0,
            gestor: Number(ownerForTraining?.gestores ?? ownerForTraining?.gestoresComerciais ?? ownerForTraining?.managers) || 0
          }}
          ownedByType={{
            comum: ownerForTraining?.trainingsByVendor?.comum || [],
            field: ownerForTraining?.trainingsByVendor?.field || [],
            inside: ownerForTraining?.trainingsByVendor?.inside || [],
            gestor: ownerForTraining?.trainingsByVendor?.gestor || []
          }}
        />)
        if (!res || res.action !== 'BUY') return
        const trainCost = Number(res.grandTotal || 0)
        if (!requireFunds(curIdx, trainCost, 'comprar Treinamento')) { setTurnLockBroadcast(false); return }
          setPlayers(ps => {
            const upd = ps.map((p, i) =>
              i !== curIdx ? p : applyTrainingPurchase(p, res)
            )
            //  CORREO: Usa turnIdx e round atuais para compras durante o turno
            broadcastState(upd, turnIdx, currentRoundRef.current)
            return upd
          })
      })()
    }

    // Compra direta (menu)
    const isDirectBuyTile = (landedOneBased === 5 || landedOneBased === 10 || landedOneBased === 43)
    if (isDirectBuyTile && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      ;(async () => {
        const cashNow = nextPlayers[curIdx]?.cash ?? myCash

        const res = await openModalAndWait(<DirectBuyModal currentCash={cashNow} />)
        if (!res) return

        if (res.action === 'OPEN') {
          const open = String(res.open || '').toUpperCase()

          if (open === 'MIX') {
            const currentMixLevel = players[curIdx]?.mixProdutos || null
            const mixOwned = players[curIdx]?.mixOwned || players[curIdx]?.mix || {}
            const r2 = await openModalAndWait(<MixProductsModal 
              currentCash={nextPlayers[curIdx]?.cash ?? myCash}
              currentLevel={currentMixLevel}
              mixOwned={mixOwned}
            />)
            if (r2 && r2.action === 'BUY') {
              const price = Number(r2.compra || 0)
              const level = String(r2.level || 'D')
              if (!requireFunds(curIdx, price, 'comprar MIX')) { setTurnLockBroadcast(false); return }
              setPlayers(ps => {
                const upd = ps.map((p,i)=>
                  i!==curIdx ? p : applyDeltas(p, {
                    cashDelta: -price,
                    mixProdutosSet: level,
                    mixBaseSet: {
                      despesaPorCliente: Number(r2.despesa || 0),
                      faturamentoPorCliente: Number(r2.faturamento || 0),
                    }
                  })
                )
                //  CORREO: Usa turnIdx e round atuais para compras durante o turno
                broadcastState(upd, turnIdx, currentRoundRef.current); return upd
              })
            }
            return
          }

          if (open === 'MANAGER') {
            const r2 = await openModalAndWait(<ManagerModal currentCash={nextPlayers[curIdx]?.cash ?? myCash} />)
            if (r2 && (r2.action === 'BUY' || r2.action === 'HIRE')) {
              const qty  = Number(r2.headcount ?? r2.qty ?? r2.managersQty ?? 1)
              const cashDelta = Number(
                (typeof r2.cashDelta !== 'undefined'
                  ? r2.cashDelta
                  : -(Number(r2.cost ?? r2.total ?? r2.totalHire ?? 0)))
              )
              const payAbs = cashDelta < 0 ? -cashDelta : 0
              if (payAbs > 0 && !requireFunds(curIdx, payAbs, 'contratar Gestor')) { setTurnLockBroadcast(false); return }
              const mexp = Number(r2.expenseDelta ?? r2.totalExpense ?? r2.maintenanceDelta ?? 0)
              setPlayers(ps => {
                const upd = ps.map((p,i)=> i!==curIdx ? p : applyDeltas(p, {
                  cashDelta,
                  gestoresDelta: qty,
                  manutencaoDelta: mexp
                }))
                //  CORREO: Usa turnIdx e round atuais para compras durante o turno
                broadcastState(upd, turnIdx, currentRoundRef.current); return upd
              })
            }
            return
          }

          if (open === 'INSIDE') {
            const r2 = await openModalAndWait(<InsideSalesModal currentCash={nextPlayers[curIdx]?.cash ?? myCash} />)
            if (r2 && (r2.action === 'BUY' || r2.action === 'HIRE')) {
              const cost = Number(r2.cost ?? r2.total ?? 0)
              if (!requireFunds(curIdx, cost, 'contratar Inside Sales')) { setTurnLockBroadcast(false); return }
              const qty  = Number(r2.headcount ?? r2.qty ?? 1)
              setPlayers(ps => {
                const upd = ps.map((p,i)=> i!==curIdx ? p : applyDeltas(p, { cashDelta: -cost, insideSalesDelta: qty }))
                //  CORREO: Usa turnIdx e round atuais para compras durante o turno
                broadcastState(upd, turnIdx, currentRoundRef.current); return upd
              })
            }
            return
          }

          if (open === 'FIELD') {
            const r2 = await openModalAndWait(<FieldSalesModal currentCash={nextPlayers[curIdx]?.cash ?? myCash} />)
            if (r2 && (r2.action === 'HIRE' || r2.action === 'BUY')) {
              const qty = Number(r2.headcount ?? r2.qty ?? 1)
              const deltas = {
                cashDelta: Number(r2.cashDelta ?? -(Number(r2.totalHire ?? r2.total ?? r2.cost ?? 0))),
                manutencaoDelta: Number(r2.expenseDelta ?? r2.totalExpense ?? 0),
                revenueDelta: Number(r2.revenueDelta ?? 0),
                fieldSalesDelta: qty,
              }
              const payAbs = deltas.cashDelta < 0 ? -deltas.cashDelta : 0
              if (payAbs > 0 && !requireFunds(curIdx, payAbs, 'contratar Field Sales')) { setTurnLockBroadcast(false); return }
              setPlayers(ps => {
                const upd = ps.map((p,i)=> i!==curIdx ? p : applyDeltas(p, deltas))
                //  CORREO: Usa turnIdx e round atuais para compras durante o turno
                broadcastState(upd, turnIdx, currentRoundRef.current); return upd
              })
            }
            return
          }

          if (open === 'COMMON') {
            const r2 = await openModalAndWait(<BuyCommonSellersModal currentCash={nextPlayers[curIdx]?.cash ?? myCash} />)
            if (r2 && r2.action === 'BUY') {
              const qty  = Number(r2.headcount ?? r2.qty ?? 0)
              const deltas = {
                cashDelta: Number(r2.cashDelta ?? -(Number(r2.totalHire ?? r2.total ?? r2.cost ?? 0))),
                vendedoresComunsDelta: qty,
                manutencaoDelta: Number(r2.expenseDelta ?? r2.totalExpense ?? 0),
                revenueDelta: Number(r2.revenueDelta ?? 0),
              }
              const payAbs = deltas.cashDelta < 0 ? -deltas.cashDelta : 0
              if (payAbs > 0 && !requireFunds(curIdx, payAbs, 'contratar Vendedores Comuns')) { setTurnLockBroadcast(false); return }
              setPlayers(ps => {
                const upd = ps.map((p,i)=> i!==curIdx ? p : applyDeltas(p, deltas))
                //  CORREO: Usa turnIdx e round atuais para compras durante o turno
                broadcastState(upd, turnIdx, currentRoundRef.current); return upd
              })
            }
            return
          }

          if (open === 'ERP') {
            const currentErpLevel = players[curIdx]?.erpLevel || null
            const erpOwned = players[curIdx]?.erpOwned || players[curIdx]?.erp || {}
            const r2 = await openModalAndWait(<ERPSystemsModal 
              currentCash={nextPlayers[curIdx]?.cash ?? myCash}
              currentLevel={currentErpLevel}
              erpOwned={erpOwned}
            />)
            if (r2 && r2.action === 'BUY') {
              const price = Number(r2.values?.compra || 0)
              if (!requireFunds(curIdx, price, 'comprar ERP')) { setTurnLockBroadcast(false); return }
              setPlayers(ps => {
                const upd = ps.map((p,i)=> i!==curIdx ? p : applyDeltas(p, { cashDelta: -price, erpLevelSet: r2.level }))
                //  CORREO: Usa turnIdx e round atuais para compras durante o turno
                broadcastState(upd, turnIdx, currentRoundRef.current); return upd
              })
            }
            return
          }

          if (open === 'CLIENTS') {
            const r2 = await openModalAndWait(<ClientsModal currentCash={nextPlayers[curIdx]?.cash ?? myCash} />)
            if (r2 && r2.action === 'BUY') {
              const cost  = Number(r2.totalCost || 0)
              if (!requireFunds(curIdx, cost, 'comprar Clientes')) { setTurnLockBroadcast(false); return }
              const qty   = Number(r2.qty || 0)
              const mAdd  = Number(r2.maintenanceDelta || 0)
              const bensD = Number(r2.bensDelta || cost)
              setPlayers(ps => {
                const upd = ps.map((p,i)=> i!==curIdx ? p : applyDeltas(p, {
                  cashDelta: -cost,
                  clientsDelta: qty,
                  manutencaoDelta: mAdd,
                  bensDelta: bensD
                }))
                //  CORREO: Usa turnIdx e round atuais para compras durante o turno
                broadcastState(upd, turnIdx, currentRoundRef.current); return upd
              })
            }
            return
          }

          if (open === 'TRAINING') {
            const ownerForTraining = players.find(isMine) || nextPlayers[curIdx]
            const r2 = await openModalAndWait(<TrainingModal
              canTrain={{
                comum:  Number(ownerForTraining?.vendedoresComuns) || 0,
                field:  Number(ownerForTraining?.fieldSales) || 0,
                inside: Number(ownerForTraining?.insideSales) || 0,
                gestor: Number(ownerForTraining?.gestores ?? ownerForTraining?.gestoresComerciais ?? ownerForTraining?.managers) || 0
              }}
              ownedByType={{
                comum: ownerForTraining?.trainingsByVendor?.comum || [],
                field: ownerForTraining?.trainingsByVendor?.field || [],
                inside: ownerForTraining?.trainingsByVendor?.inside || [],
                gestor: ownerForTraining?.trainingsByVendor?.gestor || []
              }}
            />)
            if (r2 && r2.action === 'BUY') {
              const trainCost = Number(r2.grandTotal || 0)
              if (!requireFunds(curIdx, trainCost, 'comprar Treinamento')) { setTurnLockBroadcast(false); return }
              setPlayers(ps => {
                const upd = ps.map((p,i)=> i!==curIdx ? p : applyTrainingPurchase(p, r2))
                //  CORREO: Usa turnIdx e round atuais para compras durante o turno
                broadcastState(upd, turnIdx, currentRoundRef.current); return upd
              })
            }
            return
          }
        }

        // Fallback: BUY direto
        if (res.action === 'BUY') {
          const isClientsBuy =
            res.kind === 'CLIENTS' ||
            res.modal === 'CLIENTS' ||
            typeof res.clientsQty !== 'undefined' ||
            typeof res.numClients !== 'undefined' ||
            typeof res.totalCost !== 'undefined' ||
            typeof res.maintenanceDelta !== 'undefined'

          if (isClientsBuy) {
            const cost  = Number(res.totalCost ?? res.total ?? res.amount ?? 0)
            if (!requireFunds(curIdx, cost, 'comprar Clientes')) { setTurnLockBroadcast(false); return }
            const qty   = Number(res.clientsQty ?? res.numClients ?? res.qty ?? 0)
            const mAdd  = Number(res.maintenanceDelta ?? res.maintenance ?? res.mexp ?? 0)
            const bensD = Number(res.bensDelta ?? cost)

            setPlayers(ps => {
              const upd = ps.map((p, i) =>
                i !== curIdx
                  ? p
                  : applyDeltas(p, {
                      cashDelta: -cost,
                      clientsDelta: qty,
                      manutencaoDelta: mAdd,
                      bensDelta: bensD
                    })
              )
              //  CORREO: Usa turnIdx e round atuais para compras durante o turno
              broadcastState(upd, turnIdx, currentRoundRef.current)
              return upd
            })
            return
          }

          const total = Number(res.total ?? res.amount ?? 0)
          if (!requireFunds(curIdx, total, 'esta compra')) { setTurnLockBroadcast(false); return }
          setPlayers(ps => {
            const upd = ps.map((p, i) =>
              i !== curIdx
                ? p
                : applyDeltas(p, {
                    cashDelta: -total,
                    directBuysPush: [ (res.item || { total }) ]
                  })
            )
            //  CORREO: Usa turnIdx e round atuais para compras durante o turno
            broadcastState(upd, turnIdx, currentRoundRef.current)
            return upd
          })
        }
      })()
    }

    // Inside Sales (casa especfica)
    const isInsideTile = (landedOneBased === 12 || landedOneBased === 21 || landedOneBased === 30 || landedOneBased === 42 || landedOneBased === 53)
    if (isInsideTile && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      ;(async () => {
        const res = await openModalAndWait(<InsideSalesModal currentCash={nextPlayers[curIdx]?.cash ?? myCash} />)
        if (!res || (res.action !== 'HIRE' && res.action !== 'BUY')) return
        const cost = Number(res.cost ?? res.total ?? 0)
        if (!requireFunds(curIdx, cost, 'contratar Inside Sales')) { setTurnLockBroadcast(false); return }
        const qty  = Number(res.headcount ?? res.qty ?? 1)
        setPlayers(ps => {
          const upd = ps.map((p, i) =>
            i !== curIdx ? p : applyDeltas(p, { cashDelta: -cost, insideSalesDelta: qty })
          )
          //  CORREO: Usa turnIdx e round atuais para compras durante o turno
          broadcastState(upd, turnIdx, currentRoundRef.current)
          return upd
        })
      })()
    }

    // Clientes
    const isClientsTile = [4,8,15,17,20,27,34,36,39,46,52,55].includes(landedOneBased)
    if (isClientsTile && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      ;(async () => {
        const res = await openModalAndWait(<ClientsModal currentCash={nextPlayers[curIdx]?.cash ?? myCash} />)
        if (!res || res.action !== 'BUY') return
        const cost  = Number(res.totalCost || 0)
        if (!requireFunds(curIdx, cost, 'comprar Clientes')) { setTurnLockBroadcast(false); return }
        const qty   = Number(res.qty || 0)
        const mAdd  = Number(res.maintenanceDelta || 0)
        const bensD = Number(res.bensDelta || cost)
        setPlayers(ps => {
          const upd = ps.map((p, i) =>
            i !== curIdx
              ? p
              : applyDeltas(p, {
                  cashDelta: -cost,
                  clientsDelta: qty,
                  manutencaoDelta: mAdd,
                  bensDelta: bensD
                })
          )
          //  CORREO: Usa turnIdx e round atuais para compras durante o turno
          broadcastState(upd, turnIdx, currentRoundRef.current)
          return upd
        })
      })()
    }

    // Gestor
    const isManagerTile = [18,24,29,51].includes(landedOneBased)
    if (isManagerTile && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      ;(async () => {
        const res = await openModalAndWait(<ManagerModal currentCash={nextPlayers[curIdx]?.cash ?? myCash} />)
        if (!res || (res.action !== 'BUY' && res.action !== 'HIRE')) return
        const qty  = Number(res.headcount ?? res.qty ?? res.managersQty ?? 1)
        const cashDelta = Number(
          (typeof res.cashDelta !== 'undefined'
            ? res.cashDelta
            : -(Number(res.cost ?? res.total ?? res.totalHire ?? 0)))
        )
        const payAbs = cashDelta < 0 ? -cashDelta : 0
        if (payAbs > 0 && !requireFunds(curIdx, payAbs, 'contratar Gestor')) { setTurnLockBroadcast(false); return }
        const mexp = Number(res.expenseDelta ?? res.totalExpense ?? res.maintenanceDelta ?? 0)
        setPlayers(ps => {
          const upd = ps.map((p, i) =>
            i !== curIdx ? p : applyDeltas(p, { cashDelta, gestoresDelta: qty, manutencaoDelta: mexp })
          )
          //  CORREO: Usa turnIdx e round atuais para compras durante o turno
          broadcastState(upd, turnIdx, currentRoundRef.current)
          return upd
        })
      })()
    }

    // Field Sales
    const isFieldTile = [13,25,33,38,50].includes(landedOneBased)
    if (isFieldTile && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      ;(async () => {
        const res = await openModalAndWait(<FieldSalesModal currentCash={nextPlayers[curIdx]?.cash ?? myCash} />)
        if (res && (res.action === 'HIRE' || res.action === 'BUY')) {
          const qty = Number(res.headcount ?? res.qty ?? 1)
          const deltas = {
            cashDelta: Number(res.cashDelta ?? -(Number(res.totalHire ?? res.total ?? res.cost ?? 0))),
            manutencaoDelta: Number(res.expenseDelta ?? res.totalExpense ?? 0),
            revenueDelta: Number(res.revenueDelta ?? 0),
            fieldSalesDelta: qty,
          }
          const payAbs = deltas.cashDelta < 0 ? -deltas.cashDelta : 0
          if (payAbs > 0 && !requireFunds(curIdx, payAbs, 'contratar Field Sales')) { setTurnLockBroadcast(false); return }
          setPlayers(ps => {
            const upd = ps.map((p, i) =>
              i !== curIdx ? p : applyDeltas(p, deltas)
            )
            //  CORREO: Usa turnIdx e round atuais para compras durante o turno
            broadcastState(upd, turnIdx, currentRoundRef.current)
            return upd
          })
        }
      })()
    }

    // Vendedores Comuns
    const isCommonSellersTile = [9,28,40,45].includes(landedOneBased)
    if (isCommonSellersTile && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      ;(async () => {
        const res = await openModalAndWait(<BuyCommonSellersModal currentCash={nextPlayers[curIdx]?.cash ?? myCash} />)
        if (!res || res.action !== 'BUY') return
        const qty  = Number(res.headcount ?? res.qty ?? 0)
        const deltas = {
          cashDelta: Number(res.cashDelta ?? -(Number(res.totalHire ?? res.total ?? res.cost ?? 0))),
          vendedoresComunsDelta: qty,
          manutencaoDelta: Number(res.expenseDelta ?? res.totalExpense ?? 0),
          revenueDelta: Number(res.revenueDelta ?? 0),
        }
        const payAbs = deltas.cashDelta < 0 ? -deltas.cashDelta : 0
        if (payAbs > 0 && !requireFunds(curIdx, payAbs, 'contratar Vendedores Comuns')) { setTurnLockBroadcast(false); return }
        setPlayers(ps => {
          const upd = ps.map((p, i) =>
            i !== curIdx ? p : applyDeltas(p, deltas)
          )
          //  CORREO: Usa turnIdx e round atuais para compras durante o turno
          broadcastState(upd, turnIdx, currentRoundRef.current)
          return upd
        })
      })()
    }

    // Mix de Produtos
    const isMixTile = [7,31,44].includes(landedOneBased)
    if (isMixTile && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      ;(async () => {
        const currentMixLevel = players[curIdx]?.mixProdutos || null
        const mixOwned = players[curIdx]?.mixOwned || players[curIdx]?.mix || {}
        const res = await openModalAndWait(<MixProductsModal 
          currentCash={nextPlayers[curIdx]?.cash ?? myCash}
          currentLevel={currentMixLevel}
          mixOwned={mixOwned}
        />)
        if (!res || res.action !== 'BUY') return
        const price = Number(res.compra || 0)
        if (!requireFunds(curIdx, price, 'comprar MIX')) { setTurnLockBroadcast(false); return }
        const level = String(res.level || 'D')
        setPlayers(ps => {
          const upd = ps.map((p, i) =>
            i !== curIdx
              ? p
              : applyDeltas(p, {
                  cashDelta: -price,
                  mixProdutosSet: level,
                  mixBaseSet: {
                    despesaPorCliente: Number(res.despesa || 0),
                    faturamentoPorCliente: Number(res.faturamento || 0),
                  },
                })
          )
          //  CORREO: Usa turnIdx e round atuais para compras durante o turno
          broadcastState(upd, turnIdx, currentRoundRef.current)
          return upd
        })
      })()
    }

    // Sorte & Revs
    const isLuckMisfortuneTile = [3,14,22,26,35,41,48,54].includes(landedOneBased)
    if (isLuckMisfortuneTile && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      ;(async () => {
        const res = await openModalAndWait(<SorteRevesModal />)
        if (!res || res.action !== 'APPLY_CARD') return

        const meNow = nextPlayers[curIdx] || players.find(isMine) || {}

        let cashDelta    = Number.isFinite(res.cashDelta)    ? Number(res.cashDelta)    : 0
        let clientsDelta = Number.isFinite(res.clientsDelta) ? Number(res.clientsDelta) : 0

        // O modal j calculou os efeitos baseados no estado do jogador
        // No precisamos verificar novamente aqui

        if (cashDelta < 0) {
          const need = -cashDelta
          await handleInsufficientFunds(need, 'Sorte & Revs', 'pagar', nextPlayers)
        }

        setPlayers(ps => {
          const upd = ps.map((p,i) => {
            if (i !== curIdx) return p
            let next = { ...p }
            if (cashDelta)    next.cash    = Math.max(0, (next.cash    ?? 0) + cashDelta)
            if (clientsDelta) {
              const oldClients = next.clients || 0
              next.clients = Math.max(0, oldClients + clientsDelta)
              console.log('[DEBUG] SorteReves - Clientes alterados:', oldClients, '->', next.clients, 'delta:', clientsDelta)
            }
            if (res.gainSpecialCell) {
              next.fieldSales = (next.fieldSales || 0) + (res.gainSpecialCell.fieldSales || 0)
              next.support    = (next.support    || 0) + (res.gainSpecialCell.support    || 0)
              next.gestores   = (next.gestores   || 0) + (res.gainSpecialCell.manager    || 0)
              next.gestoresComerciais = (next.gestoresComerciais || 0) + (res.gainSpecialCell.manager || 0)
              next.managers   = (next.managers   || 0) + (res.gainSpecialCell.manager    || 0)
            }
            if (res.id === 'casa_change_cert_blue') {
              next.az = (next.az || 0) + 1
              const curSet = new Set((next.trainingsByVendor?.comum || []))
              curSet.add('personalizado')
              next.trainingsByVendor = { ...(next.trainingsByVendor || {}), comum: Array.from(curSet) }
            }
            return next
          })
          //  CORREO: Usa turnIdx e round atuais para compras durante o turno
          broadcastState(upd, turnIdx, currentRoundRef.current)
          return upd
        })

        const anyDerived = res.perClientBonus || res.perCertifiedManagerBonus || res.mixLevelBonusABOnly
        if (anyDerived) {
          const me2 = nextPlayers[curIdx] || players.find(isMine) || {}
          let extra = 0
          if (res.perClientBonus)           extra += (Number(me2.clients) || 0) * Number(res.perClientBonus || 0)
          if (res.perCertifiedManagerBonus) extra += countManagerCerts(me2) * Number(res.perCertifiedManagerBonus || 0)
          if (res.mixLevelBonusABOnly) {
            const level = String(me2.mixProdutos || '').toUpperCase()
            if (level === 'A' || level === 'B') extra += Number(res.mixLevelBonusABOnly || 0)
          }
          if (extra) {
            setPlayers(ps => {
              const upd = ps.map((p,i) => i===curIdx ? { ...p, cash: (Number(p.cash)||0) + extra } : p)
              //  CORREO: Usa turnIdx e round atuais para bnus durante o turno
              broadcastState(upd, turnIdx, currentRoundRef.current); return upd
            })
          }
        }
      })()
    }

    // === AUTO-MODAIS (Faturamento / Despesas) ===
    if (crossedStart1 && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      const meNow = nextPlayers[curIdx] || {}
      const fat = Math.max(0, Math.floor(computeFaturamentoFor(meNow)))
      ;(async () => {
        await openModalAndWait(<FaturamentoDoMesModal value={fat} />)
        setPlayers(ps => {
          const upd = ps.map((p,i)=> i!==curIdx ? p : { ...p, cash: (p.cash||0) + fat })
          //  CORREO: Usa turnIdx e round atuais para faturamento durante o turno
          broadcastState(upd, turnIdx, currentRoundRef.current); return upd
        })
        appendLog(`${meNow.name} recebeu faturamento do ms: +$${fat.toLocaleString()}`)
        try { setTimeout(() => closeTop?.({ action:'AUTO_CLOSE_BELOW' }), 0) } catch {}
      })()
    }

    if (crossedExpenses23 && isMyTurn && pushModal && awaitTop) {
      openingModalRef.current = true //  CORREO: Marca ANTES de abrir
      console.log('[DEBUG]  DESPESAS OPERACIONAIS - Jogador:', nextPlayers[curIdx]?.name, 'Posio atual:', nextPlayers[curIdx]?.pos)
      const meNow = nextPlayers[curIdx] || {}
      const expense = Math.max(0, Math.floor(computeDespesasFor(meNow)))

      const lp = meNow.loanPending || {}
      //  CORREO: Emprstimo ser cobrado na prxima vez que passar pela casa de despesas operacionais
      // Verifica se h emprstimo pendente e se deve ser cobrado nesta passagem
      const shouldChargeLoan = Number(lp.amount) > 0 && !lp.charged && (lp.shouldChargeOnNextExpenses === true)
      const loanCharge = shouldChargeLoan ? Math.max(0, Math.floor(Number(lp.amount))) : 0

      console.log('[DEBUG]  DESPESAS - Valor:', expense, 'Emprstimo a cobrar:', loanCharge, 'Total:', expense + loanCharge)
      console.log('[DEBUG]  EMPRSTIMO - Detalhes:', {
        amount: Number(lp.amount),
        charged: lp.charged,
        shouldChargeOnNextExpenses: lp.shouldChargeOnNextExpenses,
        currentRound: round,
        shouldCharge: shouldChargeLoan
      })

      ;(async () => {
        await openModalAndWait(<DespesasOperacionaisModal expense={expense} loanCharge={loanCharge} />)
        const totalCharge = expense + loanCharge
        
        console.log('[DEBUG]  ANTES handleInsufficientFunds - Saldo atual:', nextPlayers[curIdx]?.cash, 'Total a pagar:', totalCharge, 'Posio:', nextPlayers[curIdx]?.pos)
        //  CORREO: Preserva a posio do jogador ao passar nextPlayers
        const canPayExpenses = await handleInsufficientFunds(totalCharge, 'Despesas Operacionais', 'pagar', nextPlayers)
        console.log('[DEBUG]  APS handleInsufficientFunds - canPayExpenses:', canPayExpenses, 'Posio atual:', nextPlayers[curIdx]?.pos)
        if (!canPayExpenses) {
          //  CORREO: No libera o turno aqui - deixa o tick() gerenciar
          // O handleInsufficientFunds j gerencia o turno quando necessrio (falncia, etc)
          console.log('[DEBUG]  canPayExpenses  false - no liberando turno aqui, deixando tick() gerenciar')
          return
        }
        
        // O handleInsufficientFunds j processou o pagamento, no precisa duplicar
        // Apenas marca o emprstimo como cobrado se necessrio
        if (shouldChargeLoan) {
          setPlayers(ps => {
            const upd = ps.map((p,i)=>{
              if (i!==curIdx) return p
              const next = { ...p }
              //  CORREO: Marca o emprstimo como cobrado e remove a flag de cobrana
              next.loanPending = { 
                ...(p.loanPending||{}), 
                charged: true, 
                chargedAtRound: round,
                shouldChargeOnNextExpenses: false //  CORREO: Remove flag aps cobrar
              }
              //  CORREO: Preserva a posio original do nextPlayers
              next.pos = nextPlayers[curIdx]?.pos ?? p.pos
              return next
            })
            //  CORREO: Usa turnIdx e round atuais para despesas durante o turno
            broadcastState(upd, turnIdx, currentRoundRef.current); return upd
          })
        }
        appendLog(`${meNow.name} pagou despesas operacionais: -$${expense.toLocaleString()}`)
        if (loanCharge > 0) appendLog(`${meNow.name} teve emprstimo cobrado: -$${loanCharge.toLocaleString()}`)
        //  CORREO: Garante que a posio seja preservada aps o processamento
        setPlayers(ps => {
          const upd = ps.map((p, i) => {
            if (i !== curIdx) return p
            // Preserva a posio original do nextPlayers
            return { ...p, pos: nextPlayers[curIdx]?.pos ?? p.pos }
          })
          console.log('[DEBUG]  DESPESAS FINALIZADAS - Jogador:', upd[curIdx]?.name, 'Posio final:', upd[curIdx]?.pos, 'Saldo final:', upd[curIdx]?.cash)
          return upd
        })
        //  CORREO: No precisa fechar modal aqui - a modal de despesas j foi fechada pelo openModalAndWait
        // O closeTop pode causar problemas com o modalLocks
      })()
    }

    // fail-safe: solta o cadeado quando todas as modais fecharem
    const start = Date.now()
    let tickAttempts = 0
    const maxTickAttempts = 200 // Limita a 20 segundos (200 * 100ms)
    
    const tick = () => {
      tickAttempts++
      
      //  CORREO: Limite de tentativas para evitar loop infinito
      if (tickAttempts > maxTickAttempts) {
        console.warn('[DEBUG]  TIMEOUT - excedeu tentativas mximas, forando desbloqueio')
        const currentLockOwner = lockOwnerRef.current
        const isLockOwner = String(currentLockOwner || '') === String(myUid)
        if (isLockOwner) {
          setTurnLockBroadcast(false)
          turnChangeInProgressRef.current = false
          //  CORREO: Fora limpeza de modalLocks se estiver travado
          if (modalLocksRef.current > 0) {
            console.warn('[DEBUG]  Forando modalLocks para 0 (timeout)')
            modalLocksRef.current = 0
            setModalLocks(0)
          }
          openingModalRef.current = false
        }
        return
      }
      
      const currentModalLocks = modalLocksRef.current
      const currentOpening = openingModalRef.current
      const currentLockOwner = lockOwnerRef.current
      const isLockOwner = String(currentLockOwner || '') === String(myUid)
      
      console.log('[DEBUG] tick - tentativa:', tickAttempts, 'modalLocks:', currentModalLocks, 'openingModalRef:', currentOpening, 'lockOwner:', currentLockOwner, 'myUid:', myUid, 'isLockOwner:', isLockOwner)
      
      //  CORREO: Verifica se uma modal est sendo aberta (evita race condition)
      if (currentOpening) {
        console.log('[DEBUG]  tick - modal est sendo aberta, aguardando...')
        setTimeout(tick, 150)
        return
      }
      
      //  CORREO: S muda turno se realmente no houver modais abertas
      //  CORREO: Verifica tambm se passou tempo suficiente desde que a ltima modal foi fechada
      // Isso garante que todas as modais (incluindo aninhadas) foram completamente fechadas
      const timeSinceLastModalClosed = lastModalClosedTimeRef.current ? (Date.now() - lastModalClosedTimeRef.current) : Infinity
      const minTimeAfterModalClose = 200 //  CORREO: Aguarda 200ms aps fechar a ltima modal antes de mudar turno
      const canChangeTurn = currentModalLocks === 0 && (timeSinceLastModalClosed >= minTimeAfterModalClose || !lastModalClosedTimeRef.current)
      
      if (canChangeTurn) {
        //  ENDGAME autoritativo: finaliza assim que no houver modais
        const td = pendingTurnDataRef.current
        const shouldEnd = !!(td?.endGame || endGamePendingRef.current)

        if (shouldEnd && !endGameFinalizedRef.current) {
          endGameFinalizedRef.current = true
          endGamePendingRef.current = false

          // pega estado mais recente (inclui faturamento/despesas j aplicados)
          const finalPlayers = Array.isArray(playersRef.current) ? playersRef.current : (td?.nextPlayers || [])
          const alive = (finalPlayers || []).filter(p => !p?.bankrupt)

          // campeo: patrimnio desc, cash desc, nome asc
          let champ = null
          if (alive.length > 0) {
            const ranked = alive
              .map(p => ({
                p,
                patrimonio: (Number(p.cash) || 0) + (Number(p.bens) || 0),
                cash: (Number(p.cash) || 0),
              }))
              .sort((a, b) =>
                (b.patrimonio - a.patrimonio) ||
                (b.cash - a.cash) ||
                String(a.p?.name || '').localeCompare(String(b.p?.name || ''))
              )
            champ = ranked[0]?.p || null
          }

          console.log('[ENDGAME] finalizando: vencedor=%s, round=5', champ?.name || '')

          // estado final local (obrigatrio)
          setPlayers(finalPlayers || [])
          setGameOver(true)
          setWinner(champ)
          setRound(5)
          currentRoundRef.current = 5

          // limpa pendncias / destrava / no avana turno
          pendingTurnDataRef.current = null
          turnChangeInProgressRef.current = false
          openingModalRef.current = false
          setTurnLockBroadcast(false)

          // propaga para todos (obrigatrio)
          broadcastState(finalPlayers || [], turnIdxRef.current, 5, true, champ, {
            round: 5,
            gameOver: true,
            winner: champ,
          })

          return
        }

        //  CORREO: Verifica se o turnIdx atual corresponde ao lockOwner
        // Se o turno mudou via SYNC, o lockOwner pode estar desatualizado
        const currentPlayer = players[turnIdx]
        const isCurrentPlayerMe = currentPlayer && String(currentPlayer.id) === String(myUid)
        
        // libera apenas se EU for o dono do cadeado OU se  minha vez e no h lockOwner
        if (isLockOwner || (isCurrentPlayerMe && !currentLockOwner)) {
          // Agora muda o turno quando todas as modais so fechadas
          const turnData = pendingTurnDataRef.current
          console.log('[DEBUG]  tick - verificando pendingTurnDataRef:', turnData ? `prximo turno: ${turnData.nextTurnIdx}` : 'null')
          if (turnData) {
            //  CORREO: Verifica novamente se no h modais abertas ou sendo abertas (double-check)
            const finalModalLocks = modalLocksRef.current
            const finalOpening = openingModalRef.current
            //  CORREO: Verifica se o turnIdx ainda  o mesmo (no mudou via SYNC)
            const finalTurnIdx = turnIdx
            const finalLockOwner = lockOwnerRef.current
            const finalIsLockOwner = String(finalLockOwner || '') === String(myUid)
            
            //  CORREO: Verifica tambm se passou tempo suficiente desde que a ltima modal foi fechada
            const finalTimeSinceLastModalClosed = lastModalClosedTimeRef.current ? (Date.now() - lastModalClosedTimeRef.current) : Infinity
            const finalCanChangeTurn = finalModalLocks === 0 && !finalOpening && (finalTimeSinceLastModalClosed >= 200 || !lastModalClosedTimeRef.current)
            
            //  CORREO: Verifica se ainda sou o dono do lock (pode ter mudado via SYNC)
            if (finalCanChangeTurn && finalTurnIdx === turnIdx && finalIsLockOwner) {
              console.log('[DEBUG]  Mudando turno - de:', turnIdx, 'para:', turnData.nextTurnIdx, 'finalModalLocks:', finalModalLocks, 'finalOpening:', finalOpening, 'timeSinceLastModalClosed:', finalTimeSinceLastModalClosed)
              
              //  FIM DE JOGO: quando a rodada tentaria ir para 6, encerramos mantendo Rodada:5
              if (turnData.endGame) {
                const finalRoundToBroadcast = Math.min(5, Number(turnData.nextRound || 5))

                // Determina vencedor (apenas jogadores vivos), pelo maior patrimnio (cash + bens)
                const alivePlayers = players.filter(p => !p?.bankrupt)
                let finalWinner = null
                if (alivePlayers.length > 0) {
                  const ranked = alivePlayers
                    .map(p => ({ ...p, patrimonio: (p.cash || 0) + (p.bens || 0) }))
                    .sort((a, b) => b.patrimonio - a.patrimonio)
                  finalWinner = ranked[0] || null
                }

                console.log('[DEBUG]  FIM DE JOGO no tick - Rodada:', finalRoundToBroadcast, 'Vencedor:', finalWinner?.name || null)
                setWinner(finalWinner)
                setGameOver(true)
                appendLog('Fim de jogo! 5 rodadas completas.')

                // Garante que HUD fique travado em 5
                setRound(prev => {
                  const finalRound = Math.min(5, Math.max(prev, finalRoundToBroadcast))
                  currentRoundRef.current = finalRound
                  return finalRound
                })

                const patch = {}
                if (turnData.nextRoundFlags) patch.roundFlags = turnData.nextRoundFlags
                patch.round = finalRoundToBroadcast
                patch.gameOver = true
                patch.winner = finalWinner

                // No muda turnIdx ao encerrar
                broadcastState(players, turnIdx, finalRoundToBroadcast, true, finalWinner, patch)
                pendingTurnDataRef.current = null
                setTurnLockBroadcast(false)
                turnChangeInProgressRef.current = false
                return
              }
              
              //  CORREO: Marca que mudana de turno est em progresso
              turnChangeInProgressRef.current = true
              
              //  CORREO: Garante que a rodada seja atualizada antes do broadcast
              // Isso garante que o broadcast sempre use o valor correto da rodada
              const roundToBroadcast = turnData.nextRound
              const shouldIncrement = turnData.shouldIncrementRound || false
              console.log('[DEBUG]  Broadcast - Rodada a ser transmitida:', roundToBroadcast, 'Rodada atual no estado:', round, 'shouldIncrement:', shouldIncrement)
              
              //  CORREO: Atualiza turnIdx e rodada antes de fazer broadcast
              // O broadcastState atualiza lastLocalStateRef com o novo turnIdx e rodada, protegendo contra estados remotos antigos
              setTurnIdx(turnData.nextTurnIdx)
              //  FIX: round monotnico no tick (NUNCA soma +1 aqui)
              // roundToBroadcast j  o valor correto calculado no advanceAndMaybeLap.
              setRound(prevRound => {
                const finalRound = Math.max(prevRound, roundToBroadcast)
                if (finalRound !== prevRound) {
                  console.log('[DEBUG]  Rodada atualizada no tick - de:', prevRound, 'para:', finalRound)
                }
                currentRoundRef.current = finalRound
                return finalRound
              })
              //  CORREO: Passa roundFlags atualizado no patch para garantir sincronizao
              const patch = {}
              if (turnData.nextRoundFlags) {
                patch.roundFlags = turnData.nextRoundFlags
              }
              if (turnData.shouldIncrementRound) {
                patch.round = roundToBroadcast
              }
              if (turnData.nextTurnPlayerId) {
                patch.turnPlayerId = turnData.nextTurnPlayerId //  CORREO MULTIPLAYER: turnPlayerId autoritativo
              }
              broadcastState(turnData.nextPlayers, turnData.nextTurnIdx, roundToBroadcast, gameOver, winner, patch)
              pendingTurnDataRef.current = null // Limpa os dados aps usar
              setTurnLockBroadcast(false)
              turnChangeInProgressRef.current = false
              console.log('[DEBUG]  Turno mudado com sucesso - Rodada:', roundToBroadcast)
            } else {
              console.log('[DEBUG]  tick - condies no atendidas, no mudando turno', { 
                finalModalLocks, 
                finalOpening, 
                finalCanChangeTurn,
                timeSinceLastModalClosed: finalTimeSinceLastModalClosed,
                finalTurnIdx, 
                turnIdx,
                finalIsLockOwner,
                isLockOwner
              })
              console.log('[DEBUG]  tick - condies no atendidas, no mudando turno', { 
                finalModalLocks, 
                finalOpening, 
                finalTurnIdx, 
                turnIdx,
                finalIsLockOwner,
                isLockOwner
              })
              // Continua verificando
              setTimeout(tick, 150)
              return
            }
          } else {
            console.log('[DEBUG]  tick - turnData  null, no mudando turno. turnIdx atual:', turnIdx, 'lockOwner:', currentLockOwner, 'isLockOwner:', isLockOwner)
            //  CORREO: Se no h turnData mas deveria haver, tenta novamente aps um delay
            // Pode ser que o pendingTurnDataRef ainda no foi preenchido
            //  CORREO: Verifica tambm se passou tempo suficiente desde que a ltima modal foi fechada
            const retryTimeSinceLastModalClosed = lastModalClosedTimeRef.current ? (Date.now() - lastModalClosedTimeRef.current) : Infinity
            const retryCanChangeTurn = currentModalLocks === 0 && (retryTimeSinceLastModalClosed >= 200 || !lastModalClosedTimeRef.current)
            
            if (isLockOwner && retryCanChangeTurn && !currentOpening) {
              //  CORREO: Limita tentativas de retry
              if (tickAttempts < 10) {
                console.log('[DEBUG]  tick - tentando novamente em 200ms (pode ser que pendingTurnDataRef ainda no foi preenchido)')
                setTimeout(tick, 200)
                return
              } else {
                console.warn('[DEBUG]  tick - excedeu tentativas de retry, liberando turnLock')
                setTurnLockBroadcast(false)
                turnChangeInProgressRef.current = false
              }
            } else {
              setTurnLockBroadcast(false)
              turnChangeInProgressRef.current = false
            }
          }
        } else {
          console.log('[DEBUG]  tick - no sou o dono do cadeado e no  minha vez, no mudando turno', { isLockOwner, isCurrentPlayerMe, currentLockOwner, myUid, turnIdx })
          //  CORREO: Se no sou o dono e no  minha vez, libera o lock
          if (!isCurrentPlayerMe) {
            setTurnLockBroadcast(false)
            turnChangeInProgressRef.current = false
          }
        }
        return
      }
      
      // Continua verificando a cada 100ms
      setTimeout(tick, 100)
    }
    //  CORREO: Adiciona um delay inicial maior para garantir que modais abertas sejam detectadas
    // Isso evita que o tick rode antes das modais serem realmente abertas
    // Verifica se h modais sendo abertas antes de iniciar o tick
    let checkAttempts = 0
    const maxCheckAttempts = 50 // Limita a 10 segundos (50 * 200ms)
    const checkBeforeTick = () => {
      checkAttempts++
      const hasOpening = openingModalRef.current
      const hasLocks = modalLocksRef.current > 0
      if ((hasOpening || hasLocks) && checkAttempts < maxCheckAttempts) {
        console.log('[DEBUG]  checkBeforeTick - modal sendo aberta ou j aberta, aguardando...', { 
          hasOpening, 
          hasLocks, 
          modalLocks: modalLocksRef.current,
          attempt: checkAttempts 
        })
        setTimeout(checkBeforeTick, 200)
        return
      }
      //  CORREO: Se excedeu tentativas ou no h modais, fora o avano do turno
      if (checkAttempts >= maxCheckAttempts) {
        console.warn('[DEBUG]  checkBeforeTick - excedeu tentativas, forando avano do turno', {
          hasOpening,
          hasLocks,
          modalLocks: modalLocksRef.current
        })
        // Fora o modalLocks para 0 se estiver travado
        if (modalLocksRef.current > 0) {
          console.warn('[DEBUG]  checkBeforeTick - forando modalLocks para 0')
          modalLocksRef.current = 0
          setModalLocks(0)
        }
        openingModalRef.current = false
      }
      // S inicia o tick se no houver modais sendo abertas
      console.log('[DEBUG]  checkBeforeTick - iniciando tick, sem modais abertas')
      tick()
    }
    //  CORREO: Delay maior para dar tempo das modais serem abertas (as modais so abertas de forma assncrona)
    // As modais so abertas dentro de blocos (async () => { ... })(), ento precisamos aguardar
    setTimeout(checkBeforeTick, 500)
  } catch (error) {
    console.error('[DEBUG] Erro em advanceAndMaybeLap:', error)
    //  BUG 2 FIX: Libera turnLock em caso de erro
    if (lockOwnerRef.current === String(myUid)) {
      setTurnLockBroadcast(false)
    }
    throw error
  } finally {
    //  BUG 2 FIX: Garante que turnLock  liberado se ainda estiver preso
    // Usa ref para verificar se ainda  o dono do lock
    if (lockOwnerRef.current === String(myUid)) {
      // Pequeno delay para evitar race condition
      setTimeout(() => {
        if (lockOwnerRef.current === String(myUid)) {
          setTurnLockBroadcast(false)
          turnChangeInProgressRef.current = false
        }
      }, 100)
    } else {
      turnChangeInProgressRef.current = false
    }
  }
  }, [
    players, round, turnIdx, roundFlags, isMyTurn, isMine,
    myUid, myCash, gameOver,
    appendLog, broadcastState,
    setPlayers, setRound, setTurnIdx, setRoundFlags,
    setTurnLockBroadcast, requireFunds, maybeFinishGame,
    pushModal, awaitTop, closeTop
  ])

  // ========= handlers menores =========
  const nextTurn = React.useCallback(() => {
    if (gameOver || !players.length) return
    const nextTurnIdx = findNextAliveIdx(players, turnIdx)
    //  CORREO MULTIPLAYER: Calcula turnPlayerId do prximo jogador
    const nextPlayer = players[nextTurnIdx]
    const nextTurnPlayerId = nextPlayer?.id ? String(nextPlayer.id) : null
    setTurnIdx(nextTurnIdx)
    broadcastState(players, nextTurnIdx, currentRoundRef.current, false, null, {
      turnPlayerId: nextTurnPlayerId //  CORREO: turnPlayerId autoritativo
    })
  }, [broadcastState, gameOver, players, round, setTurnIdx, turnIdx])

  const onAction = React.useCallback((act) => {
    if (!act?.type || gameOverRef.current || endGamePendingRef.current || endGameFinalizedRef.current) return

    if (act.type === 'ROLL'){
      //  CORREO: Single-writer - apenas o jogador da vez pode rolar
      if (!isMyTurn) {
        console.warn('[DEBUG]  onAction ROLL - no  minha vez, ignorando')
        return
      }
      //  CORREO: Verifica turnLock antes de executar
      if (turnLock) {
        console.warn('[DEBUG]  onAction ROLL - turnLock ativo, ignorando')
        return
      }
      //  CORREO: Verifica modalLocks antes de executar
      if (modalLocksRef.current > 0) {
        console.warn('[DEBUG]  onAction ROLL - h modais abertas, ignorando')
        return
      }
      //  BUG 2 FIX: try/finally para garantir liberao de turnLock
      try {
        advanceAndMaybeLap(act.steps, act.cashDelta, act.note)
      } catch (error) {
        console.error('[DEBUG] Erro em advanceAndMaybeLap:', error)
        // Libera turnLock em caso de erro
        if (lockOwnerRef.current === String(myUid)) {
          setTurnLockBroadcast(false)
        }
      }
      return
    }

    if (act.type === 'RECOVERY'){
      const recover = Math.floor(Math.random()*3000)+1000
      const cur = players.find(isMine)
      if (!cur) return
      const nextPlayers = players.map(p => (isMine(p) ? { ...p, cash: p.cash + recover } : p))
      appendLog(`${cur.name} ativou Recuperao Financeira (+$${recover})`)
      setPlayers(nextPlayers)
      broadcastState(nextPlayers, turnIdx, currentRoundRef.current)
      //  CORREO: No destrava o turno - jogador continua no seu turno aps recuperao
      // setTurnLockBroadcast(false)
      return
    }

    if (act.type === 'RECOVERY_CUSTOM'){
      const amount = Number(act.amount || 0)
      const cur = players.find(isMine)
      if (!cur) return
      const nextPlayers = players.map(p => (isMine(p) ? { ...p, cash: p.cash + amount } : p))
      appendLog(`${cur.name} recuperou +$${amount}`)
      setPlayers(nextPlayers)
      broadcastState(nextPlayers, turnIdx, currentRoundRef.current)
      //  CORREO: No destrava o turno - jogador continua no seu turno aps recuperao
      // setTurnLockBroadcast(false)
      return
    }

    if (act.type === 'RECOVERY_MODAL') {
      if (!isMyTurn || !pushModal || !awaitTop) return
      ;(async () => {
        const res = await openModalAndWait(<RecoveryModal playerName={current?.name || 'Jogador'} currentPlayer={current} />)
        if (!res) return

        if (res && res.type === 'TRIGGER_BANKRUPTCY') {
          // Fecha o fluxo atual e dispara a falncia real
          onAction({ type: 'BANKRUPT' })
          return
        }

        switch (res.type) {
          case 'FIRE':
            onAction?.({
              type: 'RECOVERY_FIRE',
              items: res.items,
              amount: res.totalCredit ?? res.amount ?? 0,
              note: res.note,
              creditByRole: res.creditByRole
            })
            break

          case 'REDUCE': {
            // --- SUPORTE: seleo nica ou mltipla ---
            const isMulti = Array.isArray(res.items) && res.items.length > 0

            // Se veio lista, marcamos selected=true para o App.jsx aceitar (ele usa o primeiro "selected")
            const items = isMulti
              ? res.items.map((i, idx) => ({
                  ...i,
                  // garante campos padronizados
                  group: String(i.group || i.tipo || '').toUpperCase(),
                  level: String(i.level || i.nivel || '').toUpperCase(),
                  credit: Number(i.credit ?? i.amount ?? 0),
                  selected: idx === 0 ? true : !!i.selected
                }))
              : undefined

            // Seleo "principal" (o App.jsx usa sel/selection quando presente)
            const first =
              (isMulti && items?.[0]) ||
              res.selection ||
              res.sel ||
              (res.group && res.level
                ? {
                    group: String(res.group).toUpperCase(),
                    level: String(res.level).toUpperCase(),
                    credit: Number(res.credit ?? res.amount ?? 0)
                  }
                : null)

            // Valor total: total/totalCredit quando mltiplo; seno credit/amount
            const amount = Number(
              (isMulti ? (res.total ?? res.totalCredit) : undefined) ??
              first?.credit ??
              res.amount ??
              0
            )

            const note =
              res.note ||
              (isMulti
                ? `Reduo mltipla +R$ ${amount.toLocaleString()}`
                : (first
                    ? `Reduo ${first.group} nvel ${first.level} +R$ ${amount.toLocaleString()}`
                    : `Reduo +R$ ${amount.toLocaleString()}`))

            onAction?.({
              type: 'RECOVERY_REDUCE',
              // passa a lista completa para o App.jsx (ele j entende 'items' e usa o primeiro selecionado)
              items,
              selection: first || null,
              amount,
              note
            })
            break
          }

          case 'LOAN': {
            // Normaliza a resposta do emprstimo
            const pack = (typeof res.amount === 'object' && res.amount !== null)
              ? res.amount
              : {
                  amount: Number(res.amount ?? 0),
                  cashDelta: Number(res.cashDelta ?? res.amount ?? 0),
                  loan: res.loan
                }

            const amount = Number(pack.amount ?? 0)
            const cashDelta = Number(pack.cashDelta ?? amount ?? 0)
            const loan = pack.loan ?? res.loan ?? {}

            onAction?.({
              type: 'RECOVERY_LOAN',
              amount,
              cashDelta,
              loan,
              note: res.note
            })
            break
          }

          default:
            if (res.amount > 0) {
              onAction?.({ type: 'RECOVERY_CUSTOM', amount: res.amount, note: res.note })
            }
        }
      })()
      return
    }

    if (act.type === 'BANKRUPT_MODAL') {
      if (!isMyTurn || !pushModal || !awaitTop) return
      ;(async () => {
        const ok = await openModalAndWait(<BankruptcyModal playerName={current?.name || 'Jogador'} />)
        if (ok) onAction?.({ type: 'BANKRUPT' })
      })()
      return
    }

    if (act.type === 'RECOVERY_FIRE') {
      const amount = Number(act.amount || 0);
      const items  = act.items || {};

      const deltas = {
        cashDelta: amount,
        vendedoresComunsDelta: -Number(items.comum  || 0),
        fieldSalesDelta:      -Number(items.field  || 0),
        insideSalesDelta:     -Number(items.inside || 0),
        gestoresDelta:        -Number(items.gestor || 0),
      };

      const curIdx = turnIdx;
      setPlayers(ps => {
        const upd = ps.map((p, i) => (i !== curIdx ? p : applyDeltas(p, deltas)));
        broadcastState(upd, turnIdx, currentRoundRef.current);
        return upd;
      });

      appendLog(`${players[curIdx]?.name || 'Jogador'}: ${act.note || 'Demisses'}`);
      //  CORREO: No destrava o turno - jogador continua no seu turno aps recuperao
      // setTurnLockBroadcast(false);
      return;
    }

    if (act.type === 'RECOVERY_LOAN') {
      const amt = Math.max(0, Number(act.amount || 0));
      if (!amt) { 
        //  CORREO: No destrava o turno - jogador continua no seu turno
        // setTurnLockBroadcast(false); 
        return; 
      }

      const curIdx = turnIdx;
      const cur = players[curIdx];

      if (cur?.loanPending && !cur.loanPending.charged) {
        appendLog(`${cur?.name || 'Jogador'} j possui um emprstimo pendente.`);
        //  CORREO: No destrava o turno - jogador continua no seu turno
        // setTurnLockBroadcast(false);
        return;
      }

      //  CORREO: Emprstimo ser cobrado na prxima vez que passar pela casa de despesas operacionais
      setPlayers(ps => {
        const upd = ps.map((p, i) =>
          i !== curIdx
            ? p
            : {
                ...p,
                cash: (Number(p.cash) || 0) + amt,
                loanPending: { 
                  amount: amt, 
                  charged: false,
                  //  CORREO: Marca que o emprstimo deve ser cobrado na prxima passagem pela casa de despesas
                  shouldChargeOnNextExpenses: true
                },
              }
        );
        broadcastState(upd, turnIdx, currentRoundRef.current);
        return upd;
      });

      appendLog(`${cur?.name || 'Jogador'} pegou emprstimo: +$${amt.toLocaleString()}`);
      //  CORREO: No destrava o turno - jogador continua no seu turno aps recuperao
      // setTurnLockBroadcast(false);
      return;
    }

    if (act.type === 'BUY_MIX' || act.kind === 'MIX_BUY' || act.type === 'DIRECT_BUY_MIX') {
      const level = String(act.level || '').toUpperCase();
      const price = Math.max(0, Number(act.price ?? 0));
      if (!['A','B','C','D'].includes(level)) { setTurnLockBroadcast(false); return; }

      const curIdx = turnIdx;
      if (!canPay(curIdx, price)) { appendLog('Saldo insuficiente para comprar MIX'); setTurnLockBroadcast(false); return }

      setPlayers(ps => {
        const upd = ps.map((p, i) => {
          if (i !== curIdx) return p;
          const mixOwned = { ...(p.mixOwned || p.mix || {}), D: true };
          mixOwned[level] = true;
          return {
            ...p,
            cash: Math.max(0, (Number(p.cash) || 0) - price),
            mixOwned,
            mix: mixOwned,
            mixLevel: level,
            mixProdutos: level
          };
        });
        broadcastState(upd, turnIdx, currentRoundRef.current);
        return upd;
      });

      appendLog(`${players[curIdx]?.name || 'Jogador'} comprou MIX nvel ${level} por -$${price.toLocaleString()}`);
      setTurnLockBroadcast(false);
      return;
    }

    if (act.type === 'BUY_ERP' || act.kind === 'ERP_BUY' || act.type === 'DIRECT_BUY_ERP') {
      const level = String(act.level || '').toUpperCase();
      const price = Math.max(0, Number(act.price ?? 0));
      if (!['A','B','C','D'].includes(level)) { setTurnLockBroadcast(false); return; }

      const curIdx = turnIdx;
      if (!canPay(curIdx, price)) { appendLog('Saldo insuficiente para comprar ERP'); setTurnLockBroadcast(false); return }

      setPlayers(ps => {
        const upd = ps.map((p, i) => {
          if (i !== curIdx) return p;
          const erpOwned = { ...(p.erpOwned || p.erp || {}), D: true };
          erpOwned[level] = true;
          return {
            ...p,
            cash: Math.max(0, (Number(p.cash) || 0) - price),
            erpOwned,
            erp: erpOwned,
            erpLevel: level,
            erpSystems: { ...(p.erpSystems || {}), level }
          };
        });
        broadcastState(upd, turnIdx, currentRoundRef.current);
        return upd;
      });

      appendLog(`${players[curIdx]?.name || 'Jogador'} comprou ERP nvel ${level} por -$${price.toLocaleString()}`);
      setTurnLockBroadcast(false);
      return;
    }

    if (act.type === 'RECOVERY_REDUCE') {
      const normLevel = (v) => String(v || '').toUpperCase();
      const normGroup = (v) => {
        const g = String(v || '').toUpperCase();
        if (g === 'MIX' || g === 'ERP') return g;
        if (g.includes('MIX')) return 'MIX';
        if (g.includes('ERP')) return 'ERP';
        return '';
      };

      const collectSelections = () => {
        if (Array.isArray(act.items) && act.items.length) {
          return act.items
            .filter(it => (it?.selected ?? true))
            .map(it => ({
              group: normGroup(it.group || it.kind),
              level: normLevel(it.level),
              credit: Math.max(0, Number(it.credit ?? it.amount ?? 0)),
            }))
            //  CORREO: No permite reduzir nvel D (bsico)
            .filter(s => (s.group === 'MIX' || s.group === 'ERP') && ['A','B','C'].includes(s.level));
        }
        const one = act.selection || act.target || null;
        if (one) {
          const s = {
            group: normGroup(one.group || one.kind),
            level: normLevel(one.level),
            credit: Math.max(0, Number(one.credit ?? one.amount ?? act.amount ?? 0)),
          };
          //  CORREO: No permite reduzir nvel D (bsico)
          if ((s.group === 'MIX' || s.group === 'ERP') && ['A','B','C'].includes(s.level)) {
            return [s];
          }
        }
        return [];
      };

      const selections = collectSelections();

      if (!selections.length) {
        const creditOnly = Math.max(0, Number(act.amount ?? 0));
        if (creditOnly > 0) {
          const curIdx = turnIdx;
          setPlayers(ps => {
            const upd = ps.map((p, i) =>
              i !== curIdx ? p : { ...p, cash: (Number(p.cash) || 0) + creditOnly }
            );
            broadcastState(upd, turnIdx, currentRoundRef.current);
            return upd;
          });
        }
        //  CORREO: No destrava o turno - jogador continua no seu turno
        // setTurnLockBroadcast(false);
        return;
      }

      const ensureOwnedFromLetter = (store, letter) => {
        const s = { A:false, B:false, C:false, D:false, ...(store || {}) };
        const L = normLevel(letter);
        if (!s.A && !s.B && !s.C && !s.D) {
          if (['A','B','C','D'].includes(L)) s[L] = true;
          else s.D = true;
        }
        return s;
      };
      const letterFromOwned = (s) => (s?.A ? 'A' : s?.B ? 'B' : s?.C ? 'C' : s?.D ? 'D' : '-');

      const curIdx = turnIdx;
      const cur = players[curIdx];

      //  CORREO: Valida que no est tentando reduzir nvel D
      const hasInvalidLevel = selections.some(s => s.level === 'D');
      if (hasInvalidLevel) {
        appendLog('No  possvel reduzir o nvel D (bsico).');
        return;
      }

      //  CORREO: Valida que no est tentando reduzir nvel j reduzido
      const reducedMix = Array.isArray(cur.reducedLevels?.MIX) ? cur.reducedLevels.MIX : [];
      const reducedErp = Array.isArray(cur.reducedLevels?.ERP) ? cur.reducedLevels.ERP : [];
      const alreadyReduced = selections.some(s => 
        (s.group === 'MIX' && reducedMix.includes(s.level)) ||
        (s.group === 'ERP' && reducedErp.includes(s.level))
      );
      if (alreadyReduced) {
        appendLog('No  possvel reduzir um nvel que j foi reduzido anteriormente.');
        return;
      }

      setPlayers(ps => {
        const upd = ps.map((p, i) => {
          if (i !== curIdx) return p;

          let mixOwned = { A:false, B:false, C:false, D:false, ...(p.mixOwned || p.mix || {}) };
          let erpOwned = { A:false, B:false, C:false, D:false, ...(p.erpOwned || p.erp || {}) };

          mixOwned = ensureOwnedFromLetter(mixOwned, p.mixProdutos);
          erpOwned = ensureOwnedFromLetter(erpOwned, p.erpSistemas);

          //  CORREO: Rastreia nveis reduzidos
          const newReducedMix = [...(Array.isArray(p.reducedLevels?.MIX) ? p.reducedLevels.MIX : [])];
          const newReducedErp = [...(Array.isArray(p.reducedLevels?.ERP) ? p.reducedLevels.ERP : [])];

          let totalCredit = 0;
          let currentMixLevel = String(p.mixProdutos || 'D').toUpperCase();
          let currentErpLevel = String(p.erpLevel || p.erpSistemas || 'D').toUpperCase();

          for (const s of selections) {
            totalCredit += Math.max(0, Number(s.credit || 0));
            if (s.group === 'MIX') {
              //  CORREO: Se est reduzindo o nvel atual, faz downgrade ANTES de remover
              if (s.level === currentMixLevel) {
                // Encontra o prximo nvel disponvel (B, C ou D)
                const levels = ['A', 'B', 'C', 'D'];
                const currentIdx = levels.indexOf(currentMixLevel);
                for (let idx = currentIdx + 1; idx < levels.length; idx++) {
                  const nextLevel = levels[idx];
                  // Verifica se o prximo nvel est disponvel (antes de remover o atual)
                  if (mixOwned[nextLevel] || nextLevel === 'D') {
                    currentMixLevel = nextLevel;
                    break;
                  }
                }
              }
              //  CORREO: Remove completamente do owned (zera a varivel)
              delete mixOwned[s.level];
              mixOwned[s.level] = false; // Garante que est explicitamente false
              //  CORREO: Adiciona  lista de reduzidos (s uma vez por nvel)
              if (!newReducedMix.includes(s.level)) {
                newReducedMix.push(s.level);
              }
            } else if (s.group === 'ERP') {
              //  CORREO: Se est reduzindo o nvel atual, faz downgrade ANTES de remover
              if (s.level === currentErpLevel) {
                // Encontra o prximo nvel disponvel (B, C ou D)
                const levels = ['A', 'B', 'C', 'D'];
                const currentIdx = levels.indexOf(currentErpLevel);
                for (let idx = currentIdx + 1; idx < levels.length; idx++) {
                  const nextLevel = levels[idx];
                  // Verifica se o prximo nvel est disponvel (antes de remover o atual)
                  if (erpOwned[nextLevel] || nextLevel === 'D') {
                    currentErpLevel = nextLevel;
                    break;
                  }
                }
              }
              //  CORREO: Remove completamente do owned (zera a varivel)
              delete erpOwned[s.level];
              erpOwned[s.level] = false; // Garante que est explicitamente false
              //  CORREO: Adiciona  lista de reduzidos (s uma vez por nvel)
              if (!newReducedErp.includes(s.level)) {
                newReducedErp.push(s.level);
              }
            }
          }

          //  CORREO: Garante que D sempre esteja disponvel se no houver outros nveis
          const hasAnyMix = mixOwned.A || mixOwned.B || mixOwned.C;
          if (!hasAnyMix) {
            mixOwned.D = true;
            //  CORREO: Se s tem D, garante que est explicitamente true
            mixOwned.A = false;
            mixOwned.B = false;
            mixOwned.C = false;
          }
          const hasAnyErp = erpOwned.A || erpOwned.B || erpOwned.C;
          if (!hasAnyErp) {
            erpOwned.D = true;
            //  CORREO: Se s tem D, garante que est explicitamente true
            erpOwned.A = false;
            erpOwned.B = false;
            erpOwned.C = false;
          }

          const mixLetter = letterFromOwned(mixOwned);
          const erpLetter = letterFromOwned(erpOwned);

          //  CORREO: Garante que o nvel atual seja atualizado corretamente aps reduo
          const finalMixLevel = mixLetter !== '-' ? mixLetter : (currentMixLevel || 'D');
          const finalErpLevel = erpLetter !== '-' ? erpLetter : (currentErpLevel || 'D');

          //  CORREO: Cria novos objetos para garantir que o React detecte a mudana
          const newMixOwned = { ...mixOwned };
          const newErpOwned = { ...erpOwned };

          return {
            ...p,
            cash: (Number(p.cash) || 0) + totalCredit,
            mixOwned: newMixOwned,
            erpOwned: newErpOwned,
            mix: newMixOwned,
            erp: newErpOwned,
            mixProdutos: finalMixLevel,
            erpLevel: finalErpLevel,
            erpSistemas: finalErpLevel,
            //  CORREO: Salva lista de nveis reduzidos
            reducedLevels: {
              MIX: newReducedMix,
              ERP: newReducedErp,
            },
          };
        });

        broadcastState(upd, turnIdx, currentRoundRef.current);
        return upd;
      });

      const total = selections.reduce((acc, s) => acc + Math.max(0, Number(s.credit || 0)), 0);
      if (selections.length === 1) {
        const s = selections[0];
        appendLog(`${cur?.name || 'Jogador'} reduziu ${s.group} nvel ${s.level} e recebeu +$${total.toLocaleString()}`);
      } else {
        appendLog(`${cur?.name || 'Jogador'} reduziu ${selections.length} nveis e recebeu +$${total.toLocaleString()}`);
      }

      //  CORREO: No destrava o turno - jogador continua no seu turno aps recuperao
      // setTurnLockBroadcast(false);
      return;
    }

    if (act.type === 'BANKRUPT'){
      const curIdx = turnIdx
      try {
        const amI = String(players[curIdx]?.id) === String(myUid)
        if (amI) setShowBankruptOverlay?.(true)
      } catch {}

      const updatedPlayers = players.map((p, i) => (i === curIdx ? { ...p, bankrupt: true } : p))
      appendLog(`${players[curIdx]?.name || 'Jogador'} declarou FALNCIA.`)

      const alive = countAlivePlayers(updatedPlayers)
      if (alive <= 1) {
        const winnerIdx = updatedPlayers.findIndex(p => !p?.bankrupt)
        setWinner(winnerIdx >= 0 ? updatedPlayers[winnerIdx] : null)
        setPlayers(updatedPlayers)
        setGameOver(true)
        setTurnLockBroadcast(false)
        broadcastState(updatedPlayers, turnIdx, round, true, winnerIdx >= 0 ? updatedPlayers[winnerIdx] : null)
        return
      }

      const nextIdx = findNextAliveIdx(updatedPlayers, curIdx)
      setPlayers(updatedPlayers)
      setTurnIdx(nextIdx)
      setTurnLockBroadcast(false)
      broadcastState(updatedPlayers, nextIdx, currentRoundRef.current)
      console.log('[DEBUG]  advanceAndMaybeLap finalizada (falncia) - posio final:', updatedPlayers[nextIdx]?.pos)
      return
    }
    console.log('[DEBUG]  advanceAndMaybeLap finalizada normalmente - posio final:', nextPlayers[curIdx]?.pos)
  }, [
    players, round, turnIdx, isMyTurn, isMine, myUid, myCash,
    gameOver, appendLog, broadcastState,
    setPlayers, setRound, setTurnIdx, setTurnLockBroadcast, setGameOver, setWinner,
    requireFunds, pushModal, awaitTop, closeTop, setShowBankruptOverlay
  ])

  // ====== efeitos de destrava automtica ======

  //  CORREO: Este useEffect foi removido - duplicado do anterior
  // A lgica de atualizao de lockOwner e limpeza de pendingTurnDataRef
  // est no useEffect anterior (linhas 100-117)

  // a) quando no houver modal aberta e ainda houver lock, tenta destravar
  React.useEffect(() => {
    if (modalLocks === 0 && turnLock && !openingModalRef.current) {
      const currentLockOwner = lockOwnerRef.current
      const isLockOwner = String(currentLockOwner || '') === String(myUid)
      
      if (isLockOwner) {
        console.log('[DEBUG]  Destravando turnLock - modalLocks: 0, sou o lockOwner')
        setTurnLockBroadcast(false)
        turnChangeInProgressRef.current = false
      } else if (!isMyTurn) {
        //  CORREO: Se no  minha vez e no sou o lockOwner, libera o turnLock
        // Isso evita que o boto fique travado aps sincronizao
        console.log('[DEBUG]  Destravando turnLock - no  minha vez e no sou lockOwner')
        setTurnLockBroadcast(false)
        turnChangeInProgressRef.current = false
      }
    }
  }, [modalLocks, turnLock, lockOwner, myUid, isMyTurn, setTurnLockBroadcast])

  // b) quando virar "minha vez" e no houver modal, garanto unlock local
  React.useEffect(() => {
    if (isMyTurn && modalLocks === 0 && turnLock && !openingModalRef.current) {
      const currentLockOwner = lockOwnerRef.current
      const isLockOwner = String(currentLockOwner || '') === String(myUid)
      
      if (isLockOwner) {
        console.log('[DEBUG]  Destravando turnLock -  minha vez e sou o lockOwner')
        setTurnLockBroadcast(false)
        turnChangeInProgressRef.current = false
      } else if (!currentLockOwner) {
        //  CORREO: Se  minha vez mas no h lockOwner, libera o turnLock
        // Isso garante que o boto seja habilitado quando  minha vez
        console.log('[DEBUG]  Destravando turnLock -  minha vez mas no h lockOwner')
        setTurnLockBroadcast(false)
        turnChangeInProgressRef.current = false
      }
    }
  }, [isMyTurn, modalLocks, turnLock, lockOwner, myUid, setTurnLockBroadcast])
  
  //  CORREO: Cleanup ao desmontar componente
  React.useEffect(() => {
    return () => {
      // Limpa timeouts e refs ao desmontar
      if (turnLockTimeoutRef.current) {
        clearTimeout(turnLockTimeoutRef.current)
        turnLockTimeoutRef.current = null
      }
      turnChangeInProgressRef.current = false
      openingModalRef.current = false
    }
  }, [])

  return {
    advanceAndMaybeLap,
    onAction,
    nextTurn,
    modalLocks,
    lockOwner,
  }
}
